import Vuex from 'vuex';
import captureActions from './actions/captures';
import communityActions from './actions/communities';
import crawlerActions from './actions/crawlers';
import documentActions from './actions/documents';
import generalActions from './actions/general';
import identityActions from './actions/identities';
import jobActions from './actions/jobs';
import parserActions from './actions/parsers';
import projectActions from './actions/projects';
import searchActions from './actions/search';
import sitemapActions from './actions/sitemaps';
import getters from './getters';
import mutations from './mutations';

export default (seed={}) => {
  return new Vuex.Store({
    actions: {
      ...captureActions,
      ...communityActions,
      ...crawlerActions,
      ...documentActions,
      ...generalActions,
      ...identityActions,
      ...jobActions,
      ...parserActions,
      ...projectActions,
      ...searchActions,
      ...sitemapActions,
    },
    getters,
    mutations,
    state: Object.assign({
      // model sets
      captureModels: {},
      communityModels: {},
      crawlerModels: {},
      documentModels: {},
      jobModels: {},
      parserModels: {},
      placeholderModels: {},
      projectModels: {},
      sitemapModels: {},
      // application state
      currentDialog: null,
      currentCommunity: {
        id: null,
        name: null,
      },
      currentUser: {
        id: null,
        networkRoles: [],
        memberships: [],
        roles: [],
      },
      documentJobTasks: null,
      entitlements: null,
      notifications: [],
    }, seed),
  });
};
