<template>
  <main>
    <div class="m-import-manager__header --split">
      <h1>Imported {{ type }} identities</h1>
      <res-button paddingless @click.prevent="newIdentity()">
        <res-icon name="add" size="medium" /> <button>New identity</button>
      </res-button>
    </div>

    <form class="m-import-manager__fieldset" @submit.prevent>
      <res-input>
        <label>Search</label>
        <input v-model.lazy="identity" type="text" placeholder="Search..." />
        <res-icon slot="inner-right" name="search" />
      </res-input>
    </form>

    <div class="m-import-manager__table">
      <res-table zebra="true" class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Identity</th>
              <th>Chorus ID</th>
              <th colspan="2">Community ID</th>
            </tr>
          </thead>
          <tbody>
            <identities-row v-for="result in results" :key="result.id" :model="result"/>
          </tbody>
        </table>
      </res-table>
      <footer>
        <span v-if="loaded">
          Found {{ formatNumber(totalCount) }} results
        </span>
        <res-pagination
          :page="page"
          :count="totalCount"
          :display="perPage"
          indicator="page"
          pagination="text-only"
          navigation="text"
          @change="page=$event.target.page"
        />
      </footer>
    </div>
  </main>
</template>

<script>
import gql from 'graphql-tag';
import { mapState, mapActions } from 'vuex';
import { formatNumber } from '../lib/formatting';
import IdentitiesRow from '../components/tables/identities-row.vue';

const createForm = {
  fields: {
    identity: {
      label: 'Identity',
      type: 'text'
    },
    chorusId: {
      label: 'Chorus ID',
      type: 'number'
    }
  },
  submit: 'Create Identity'
};

export default {
  components: {
    IdentitiesRow
  },
  props: {
    type: { type: String, required: true }
  },
  data() {
    return {
      loaded: false,
      identity: '',
      page: 1,
      perPage: 25,
      results: [],
      totalCount: 0,
    };
  },
  computed: {
    ...mapState([
      'currentCommunity'
    ]),
    apiType() {
      return `${ this.type.toUpperCase() }_IDENTITY`;
    }
  },
  methods: {
    ...mapActions([
      'createIdentity',
      'setDialog',
    ]),
    formatNumber,
    newIdentity() {
      this.setDialog({ tag: 'input-form', form: createForm })
        .then(async (data) => {
          if (!data.identity || !data.chorusId) return;
          await this.createIdentity({
            communityId: this.currentCommunity.id,
            identity: data.identity,
            chorusId: Number(data.chorusId),
            type: this.apiType,
          });
          this.$apollo.queries.results.refresh();
        });
    }
  },
  apollo: {
    results: {
      client: 'importer',
      query: gql`
        query SearchIdentities($input: SearchImporterMappedIdentitiesInput!) {
          search: searchImporterMappedIdentities(input: $input) {
            pageNumber
            results {
              id
              identity
              communityId
              chorusId
              type
            }
            totalCount
          }
        }
      `,
      variables() {
        return {
          input: {
            communityId: this.currentCommunity.id,
            identity: this.identity || undefined,
            pageNumber: this.page,
            perPage: this.perPage,
            type: this.apiType,
          }
        };
      },
      update(data) {
        this.loaded = true;
        this.page = data.search.pageNumber;
        this.totalCount = data.search.totalCount;
        return data.search.results;
      }
    }
  }
};
</script>
