import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';
import { CrawlerBaseAttrs } from './crawlers';

const SitemapBaseAttrs = gql`
  fragment SitemapBaseAttrs on ImporterSitemapEntry {
    blacklistUrls
    id
    name
    status
    type
    url
  }
`;

export default {

  async fetchSitemap({ state }, params) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchSitemap($id: Int!) {
          sitemap: importerSitemapEntry(id: $id) {
            ...SitemapBaseAttrs
            allowsCrawling
            communityDelegationId
            communityDelegationMode
            error
            errorMessage
            fetchedAt
            parentSitemap {
              id
              name
            }
            project {
              id
              name
              communityDelegations {
                constituentCommunityId
                id
                name
              }
            }
            currentCrawler {
              ...CrawlerBaseAttrs
              sitemapEntry {
                id
                name
                parentId
              }
            }
          }
        }
        ${ SitemapBaseAttrs }
        ${ CrawlerBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, {
      sitemapModels: [data.sitemap, data.sitemap.parentSitemap],
      projectModels: data.sitemap.project,
      crawlerModels: data.sitemap.currentCrawler,
      communityModels: data.sitemap.project.communityDelegations,
    });
    this.commit(cmd.PATCH_MODELS, {
      data,
      sitemapModels: 'sitemap.currentCrawler.sitemapEntry',
    });
    return Promise.resolve(state.sitemapModels[params.id]);
  },

  async createSitemap({ state }, input) {
    // input = CreateImporterSitemapEntryInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateSitemap($input: CreateImporterSitemapEntryInput!) {
          sitemap: createImporterSitemapEntry(input: $input) {
            ...SitemapBaseAttrs
          }
        }
        ${ SitemapBaseAttrs }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, { sitemapModels: data.sitemap });
    return Promise.resolve(state.sitemapModels[data.sitemap.id]);
  },

  async updateSitemap({ state }, params) {
    // params.id = Int
    // params.input = UpdateImporterSitemapEntryInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateSitemap($id: Int!, $input: UpdateImporterSitemapEntryInput!) {
          sitemap: updateImporterSitemapEntry(id: $id, input: $input) {
            ...SitemapBaseAttrs
            communityDelegationId
            communityDelegationMode
          }
        }
        ${ SitemapBaseAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { sitemapModels: data.sitemap });
    return Promise.resolve(state.sitemapModels[params.id]);
  },

  async recountSitemap({ state }, params) {
    // params.id = Int
    // params.deep = Boolean
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation RecountSitemap($id: Int!, $deep: Boolean=false) {
          sitemap: recountImporterSitemapEntry(id: $id, deep: $deep) {
            ...SitemapBaseAttrs
            activeDocumentsCount
            archivedDocumentsCount
            failedDocumentsCount
            importedDocumentsCount
            newDocumentsCount
          }
        }
        ${ SitemapBaseAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { sitemapModels: data.sitemap });
    return Promise.resolve(state.sitemapModels[params.id]);
  },

  async captureSitemap({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CaptureSitemap($id: Int!) {
          sitemap: captureImporterSitemapEntry(id: $id) {
            ...SitemapBaseAttrs
          }
        }
        ${ SitemapBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { sitemapModels: data.sitemap });
    return Promise.resolve(state.sitemapModels[params.id]);
  },

  unloadSitemap({ state }, params) {
    const model = state.sitemapModels[params.id];
    this.commit(cmd.SET_MODELS, {
      sitemapModels: { id: model.id, name: model.name },
    });
  }
};
