<template>
  <main>
    <section>
      <div class="m-import-manager__header">
        <h1>Project: {{ project.name }}</h1>
        <res-icon v-if="saving" class="m-l-1" name="more" size="medium" />
      </div>
      <form>
        <fieldset>
          <div class="m-import-manager__fieldset">
            <div class="m-import-manager__fieldset__input">
              <res-input>
                <label>Name</label> <input v-model.lazy="mutable.name" type="text" placeholder="Name" />
              </res-input>
            </div>
            <div class="m-import-manager__fieldset__input m-l-1">
              <div v-if="isLoaded" class="m-import-manager__actionset">
                <res-button paddingless @click.prevent="newParser()">
                  <res-icon name="add" size="medium" /> <button>New parser</button>
                </res-button>
                <res-button class="m-l-1" paddingless @click.prevent="editParser()">
                  <res-icon name="edit" size="medium" /> <button>Edit parser</button>
                </res-button>
              </div>
              <res-select>
                <label>Markup Parser</label>
                <select v-model="mutable.documentParserId" name="parser">
                  <option v-for="parser in parserModelsList" :key="parser.id" :value="parser.id">{{ parser.name }}</option>
                </select>
              </res-select>
            </div>
          </div>
        </fieldset>
        <dl class="m-import-manager__defs">
          <dt>
            <res-switch :checked="mutable.createRedirects" @change="mutable.createRedirects=$event.target.checked">
              <input type="checkbox" /> <label>Create redirects</label>
            </res-switch>
          </dt>
          <dd>Create redirects for canonical URLs and AMP pages.</dd>
          <dt>
            <res-switch :checked="mutable.createAuthors" @change="mutable.createAuthors=$event.target.checked">
              <input type="checkbox" /> <label>Create authors</label>
            </res-switch>
          </dt>
          <dd>Add author permissions and profiles to users with email addresses.</dd>
          <dt>
            <res-switch :checked="mutable.enableFrontPage" @change="mutable.enableFrontPage=$event.target.checked">
              <input type="checkbox" /> <label>Add to front page</label>
            </res-switch>
          </dt>
          <dd>Automatically add documents to the "Front Page" group.</dd>
          <dt>
            <res-switch :checked="!mutable.active" @change="mutable.active=!$event.target.checked">
              <input type="checkbox" /> <label>Disable capture</label>
            </res-switch>
          </dt>
          <dd>Prevents further captures from the remote site.</dd>
        </dl>
      </form>
    </section>

    <section>
      <div class="m-import-manager__header --split">
        <h2>Sitemaps</h2>
        <res-button paddingless @click.prevent="newSitemap()">
          <res-icon name="add" size="medium" /> <button>New sitemap</button>
        </res-button>
      </div>
      <sitemaps-table v-if="isLoaded" :projectId="id" :parentId="null"/>
    </section>
  </main>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { formatMutable, mutableHasChanged } from '../lib/utils';
import { projectDefault } from '../store/models/defaults';
import SitemapsTable from '../components/sitemaps-table.vue';

const mutableSchema = {
  active: '',
  createRedirects: '',
  createAuthors: '',
  enableFrontPage: '',
  documentParserId: '',
  name: '',
};

const createParserForm = {
  fields: {
    name: {
      label: 'Name',
      type: 'text'
    }
  },
  submit: 'Create Parser'
};

const createSitemapForm = {
  fields: {
    name: {
      label: 'Sitemap Name',
      type: 'text'
    },
    url: {
      label: 'URL',
      type: 'text'
    }
  },
  submit: 'Create Sitemap'
};

export default {
  components: {
    SitemapsTable
  },
  props: {
    id: { type: Number, required: true }
  },
  data() {
    return {
      saving: false,
      // assume projects are active by default (majority case)
      // to eliminate some noisy animation from res-switch
      mutable: formatMutable(mutableSchema, {
        active: true,
        createAuthors: false,
        createRedirects: false,
        enableFrontPage: false,
      }),
    };
  },
  computed: {
    ...mapState([
      'currentCommunity'
    ]),
    ...mapGetters([
      'parserModelsList'
    ]),
    project() {
      return this.$store.state.projectModels[this.id] || projectDefault;
    },
    isLoaded() {
      return !!this.project.id;
    },
    isChanged() {
      return this.isLoaded && mutableHasChanged(this.mutable, this.project);
    }
  },
  mounted() {
    for (let field of Object.keys(mutableSchema)) {
      this.$watch(`mutable.${field}`, this.save);
    }

    this.fetchProject({ id: this.id })
      .then(project => this.mutable = formatMutable(mutableSchema, project));
  },
  methods: {
    ...mapActions([
      'createParser',
      'createSitemap',
      'fetchProject',
      'setDialog',
      'updateProject',
    ]),
    editParser() {
      if (this.mutable.documentParserId) {
        this.$router.push({ name: 'parser', params: { id: this.mutable.documentParserId } });
      }
    },
    newParser() {
      this.setDialog({ tag: 'input-form', form: createParserForm })
        .then(data => {
          if (!data.name) return;
          this.createParser({ name: data.name });
        });
    },
    newSitemap() {
      this.setDialog({ tag: 'input-form', form: createSitemapForm })
        .then(data => {
          if (!data.name || !data.url) return;
          this.createSitemap({
            projectId: this.id,
            name: data.name,
            url: data.url,
          });
        });
    },
    save() {
      if (!this.isChanged || this.saving) return;
      this.saving = true;
      this.updateProject({ id: this.id, input: this.mutable }).then(project => {
        this.mutable = formatMutable(mutableSchema, project);
        this.saving = false;
      });
    }
  }
};
</script>
