<template>
  <main>
    <section>
      <div class="m-import-manager__header --split">
        <h1>Projects</h1>
        <res-button paddingless @click.prevent="newProject()">
          <res-icon name="add" size="medium" /> <button>New Project</button>
        </res-button>
      </div>
      <res-table zebra="true" class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Title</th>
              <th>Crawler</th>
              <th>Documents</th>
              <th>Imported</th>
              <th>Failed</th>
              <th>New</th>
              <th>Archived</th>
            </tr>
          </thead>
          <tbody>
            <projects-row v-for="item in projectsTableItems" :key="[item._type, item.id].join(':')" :model="item"/>
          </tbody>
        </table>
      </res-table>
    </section>

    <documents-search :projects="projectSearchOptions">
      <h1>Document Search</h1>
    </documents-search>
  </main>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import DocumentsSearch from '../components/documents-search.vue';
import ProjectsRow from '../components/tables/projects-row.vue';

export default {
  components: {
    DocumentsSearch,
    ProjectsRow,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState([
      'currentCommunity'
    ]),
    ...mapGetters([
      'projectModelsList',
      'sitemapModelsList',
    ]),
    projectsTableItems() {
      return this.projectModelsList.reduce((memo, project) => {
        memo.push(project);
        memo = memo.concat(this.sitemapsForProject(project));
        return memo;
      }, []);
    },
    projectSearchOptions() {
      return this.projectModelsList.reduce((memo, project) => {
        memo[project.id] = {
          id: project.id,
          name: project.name,
          sitemaps: this.sitemapsForProject(project),
        };
        return memo;
      }, {});
    },
  },
  mounted() {
    this.fetchProjects();
  },
  methods: {
    ...mapActions([
      'fetchProjects',
      'setDialog',
    ]),
    sitemapsForProject(project) {
      return this.sitemapModelsList.filter(sitemap => sitemap.projectId === project.id);
    },
    newProject() {
      this.setDialog({ tag: 'create-project' });
    }
  }
};
</script>
