<template>
  <main>
    <app-breadcrumbs :project="project" :sitemap="sitemap"/>

    <section>
      <div class="m-import-manager__header">
        <h1>Sitemap <span v-if="isLoaded">{{ isIndex ? 'Index' : 'Urlset' }}: {{ sitemap.name }}</span></h1>
        <res-icon v-if="saving" class="m-l-1" name="more" size="medium" />
      </div>
      <div class="m-import-manager__fieldset">
        <div class="m-import-manager__fieldset__input">
          <res-input>
            <label>Name</label>
            <input v-model.lazy="mutable.name" type="text" placeholder="Name" />
          </res-input>
        </div>
        <div v-if="communityOptions" class="m-import-manager__fieldset__input m-l-1">
          <res-select>
            <label>Community Selection</label>
            <select v-model="communityMode">
              <option v-for="(opt, index) in communityOptions" :key="index" :value="opt.value">{{ opt.label }}</option>
            </select>
          </res-select>
        </div>
      </div>

      <res-button paddingless class="m-b-1 m-t-1" @click.prevent="advanced = !advanced">
        <button>Advanced</button> <res-icon :name="advanced ? 'minus' : 'add'" size="large" />
      </res-button>
      <div v-if="advanced">
        <res-input>
          <label>Exclude URLs
            <res-button id="exclude-urls-tooltip" paddingless @click.prevent>
              <res-icon size="small" name="tooltip"/><button/>
            </res-button>
            <res-dropdown for="exclude-urls-tooltip" placement="top" design="dark">URLs that should be ignored<br /> while processing this sitemap.</res-dropdown>
          </label>
          <textarea v-model.lazy="mutable.blacklistUrls" placeholder="URLs, one per line..." />
        </res-input>
      </div>
    </section>

    <section>
      <div class="m-import-manager__header">
        <h2>Details</h2>
      </div>
      <crawler-status :id="sitemap.currentCrawlerId" :crawlable="sitemap.allowsCrawling" :sitemapId="id"/>
      <dl class="m-import-manager__defs">
        <dt>Status</dt>
        <dd>
          <span><res-icon :name="statusIcon" size="medium" /> {{ sitemap.status }}</span>
        </dd>
        <dt>URL</dt>
        <dd><a :href="sitemap.url" target="_blank">{{ sitemap.url }}</a></dd>
        <dt>Last fetched</dt>
        <dd>{{ sitemap.fetchedAt ? formatDateTime(sitemap.fetchedAt) : 'N/A' }}</dd>
        <dt v-if="sitemap.error">Error</dt>
        <dd v-if="sitemap.error">{{ sitemap.error }}: {{ sitemap.errorMessage }}</dd>
      </dl>
    </section>

    <sitemaps-search v-if="isIndex" :parentId="id" :collapsable="true" :version="lastFetch">
      <h2>Urlsets</h2>
    </sitemaps-search>
    <documents-search
      v-if="isUrlset"
      :sitemapEntryId="id"
      :communityScopeId="communityScopeId"
      :collapsable="true"
      :version="lastFetch"
    >
      <h2>Documents</h2>
    </documents-search>
  </main>
</template>

<script>
import Polling from '../components/mixins/polling';
import { mapActions } from 'vuex';
import { formatDateTime, entryStatusIcons } from '../lib/formatting';
import { formatMutable, mutableHasChanged, compactMutable } from '../lib/utils';
import { sitemapDefault, projectDefault } from '../store/models/defaults';
import AppBreadcrumbs from '../components/app-breadcrumbs.vue';
import CrawlerStatus from '../components/crawler-status.vue';
import DocumentsSearch from '../components/documents-search.vue';
import SitemapsSearch from '../components/sitemaps-search.vue';

const mutableSchema = {
  blacklistUrls: '',
  communityDelegationId: null,
  communityDelegationMode: null,
  name: '',
};

export default {
  components: {
    AppBreadcrumbs,
    CrawlerStatus,
    DocumentsSearch,
    SitemapsSearch,
  },
  mixins: [Polling],
  props: {
    id: { type: Number, required: true }
  },
  data() {
    return {
      advanced: false,
      lastFetch: null,
      mutable: formatMutable(mutableSchema),
      saving: false,
    };
  },
  computed: {
    sitemap() {
      return this.$store.state.sitemapModels[this.id] || sitemapDefault;
    },
    parent() {
      return this.$store.state.sitemapModels[this.sitemap.parentId] || sitemapDefault;
    },
    project() {
      return this.$store.state.projectModels[this.sitemap.projectId] || projectDefault;
    },
    communityDelegation() {
      return this.$store.state.communityModels[this.mutable.communityDelegationId] || null;
    },
    isLoaded() {
      return !!this.sitemap.id;
    },
    isRoot() {
      return this.isLoaded && !this.parent.id;
    },
    isIndex() {
      return this.isRoot && this.sitemap.type !== 'SITEMAP_URLSET';
    },
    isUrlset() {
      return this.isLoaded && this.sitemap.type === 'SITEMAP_URLSET';
    },
    isChanged() {
      return this.isLoaded && mutableHasChanged(this.mutable, this.sitemap);
    },
    statusIcon() {
      return entryStatusIcons[this.sitemap.status] || entryStatusIcons.CREATED;
    },
    communityScopeId() {
      if (this.communityDelegation) {
        return this.communityDelegation.constituentCommunityId;
      } else if (this.mutable.communityDelegationMode === 'AUTO_DELEGATION') {
        return this.$store.state.currentCommunity.id;
      }
      return null;
    },
    communityOptions() {
      const delegations = this.$store.getters.communityDelegationsList;
      if (!delegations.length) return null;
      return [
        {
          value: 'URL_DELEGATION',
          label: 'Import into URL-matched community'
        },
        {
          value: 'AUTO_DELEGATION',
          label: `Import into ${ this.$store.state.currentCommunity.name }`
        }
      ].concat(delegations.map(d => ({
        value: `AUTO_DELEGATION:${ d.id }`,
        label: `Import into ${ d.name }`
      })));
    },
    communityMode: {
      get() {
        const fields = [this.mutable.communityDelegationMode];
        if (this.mutable.communityDelegationId) {
          fields.push(this.mutable.communityDelegationId);
        }
        return fields.join(':');
      },
      set(val) {
        const fields = val.split(':');
        this.mutable = Object.assign({}, this.mutable, {
          communityDelegationMode: fields[0],
          communityDelegationId: fields[1] ? Number(fields[1]) : null
        });
      }
    }
  },
  mounted() {
    this.startImmediatePolling(() => {
      this.lastFetch = Date.now();
      this.fetchSitemap({ id: this.id, detail: true })
        .then(sitemap => this.mutable = formatMutable(mutableSchema, sitemap));
    }, 15000);

    for (let field of Object.keys(mutableSchema)) {
      this.$watch(`mutable.${field}`, this.save);
    }
  },
  destroyed() {
    if (!this.isRoot) {
      this.unloadSitemap({ id: this.id });
    }
  },
  methods: {
    ...mapActions([
      'fetchSitemap',
      'unloadSitemap',
      'updateSitemap',
    ]),
    formatDateTime,
    save() {
      if (this.isChanged && !this.saving) {
        this.saving = true;
        this.updateSitemap({ id: this.id, input: compactMutable(mutableSchema, this.mutable) })
          .then(sitemap => {
            this.saving = false;
            this.mutable = formatMutable(mutableSchema, sitemap);
            this.lastFetch = Date.now();
          });
      }
    }
  }
};
</script>
