<template>
  <form @submit.prevent>
    <fieldset>
      <res-input count="75">
        <label>Note</label>
        <input v-model="revisionNote" type="text" />
      </res-input>
    </fieldset>
    <footer>
      <res-button design="primary" @click.prevent="save()">
        <button type="submit">Save copy</button>
      </res-button>
      <res-button design="secondary" class="m-l-1" @click.prevent="close()">
        <button type="button">Cancel</button>
      </res-button>
    </footer>
  </form>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  data() {
    return {
      revisionNote: ''
    };
  },
  computed: {
    ...mapState([
      'currentDialog',
    ])
  },
  methods: {
    ...mapActions([
      'setDialog',
    ]),
    save() {
      this.currentDialog.resolve(this.revisionNote || undefined);
      this.setDialog(null);
    },
    close() {
      this.setDialog(null);
    }
  }
};
</script>
