<template>
  <div>
    <res-banner
      v-for="notice in notifications"
      :key="notice.id"
      :design="designForNotice(notice)"
      :timeout="notice.type === 'success'"
      inert
      @close="deleteNotice(notice)"
    >
      <res-icon v-if="notice.type === 'success'" name="check" />
      <span><strong v-if="notice.count > 1">({{ notice.count }})</strong> {{ notice.message }}</span>
    </res-banner>
  </div>
</template>

<script>
import { DELETE_NOTICE } from '../store/commands';
import { mapState } from 'vuex';

const notificationDesign = {
  error: 'negative',
  success: 'positive',
};

export default {
  computed: {
    ...mapState([
      'notifications',
    ])
  },
  methods: {
    deleteNotice(notice) {
      this.$store.commit(DELETE_NOTICE, notice.id);
    },
    designForNotice(notice) {
      return notificationDesign[notice.type] || 'neutral';
    },
  }
};
</script>
