<template>
  <div>
    <textarea :value="currentDialog.value" class="m-import-manager__console" readonly />
    <footer>
      <res-button design="secondary" @click.prevent="close()">
        <button type="button">Okay</button>
      </res-button>
    </footer>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  computed: {
    ...mapState([
      'currentDialog',
    ])
  },
  methods: {
    ...mapActions([
      'setDialog',
    ]),
    close() {
      this.setDialog(null);
    }
  }
};
</script>
