<template>
  <main>
    <section>
      <div class="m-import-manager__header --split">
        <h1>Parser: {{ parser.name || formatDateTime(parser.createdAt) }}</h1>
        <span v-if="isRevision">
          Revision of <router-link :to="routeTo('parser', revisioner.id)">{{ revisioner.name }}</router-link>
        </span>
        <res-split-button v-else :disabled="!isChanged">
          <ul>
            <li active="#"><a href="#" @click.prevent="saveParser('SAVE')">Save</a></li>
            <li><a href="#" @click.prevent="saveParser('SAVE_COPY')">Save with history</a></li>
          </ul>
        </res-split-button>
      </div>

      <fieldset v-show="!isRevision">
        <res-input>
          <label>Name</label>
          <input v-model="mutable.name" type="text" placeholder="Name" />
        </res-input>
      </fieldset>
      <fieldset>
        <res-input>
          <label>Parser JSON</label>
          <textarea v-model="mutable.parserJson" :readonly="isRevision" style="height:70vh;font-family:'Consolas',monospace;" />
        </res-input>
      </fieldset>

      <dl v-if="isRevision" class="m-import-manager__defs">
        <dt>Capture type</dt>
        <dd>{{ parser.captureType }}</dd>
        <dt>Group taxonomy type</dt>
        <dd>{{ parser.groupTaxonomyType || 'none' }}</dd>
      </dl>
      <template v-else>
        <res-button paddingless class="m-b-1 m-t-1" @click.prevent="advanced = !advanced">
          <button>Advanced</button> <res-icon :name="advanced ? 'minus' : 'add'" size="large" />
        </res-button>
        <div v-if="advanced" class="m-import-manager__fieldset">
          <div class="m-import-manager__fieldset__input">
            <res-select>
              <label>Capture type</label>
              <select v-model="mutable.captureType">
                <option v-for="type in parser.captureTypes" :key="type" :value="type">{{ type }}</option>
              </select>
            </res-select>
          </div>
          <div class="m-import-manager__fieldset__input m-l-1">
            <res-select>
              <label>Group taxonomy type</label>
              <select v-model="mutable.groupTaxonomyType">
                <option value="">None</option>
                <option v-for="type in parser.groupTaxonomyTypes" :key="type" :value="type">{{ type }}</option>
              </select>
            </res-select>
          </div>
        </div>
      </template>
    </section>

    <section v-if="!isRevision && revisions.length" class="m-import-manager__table">
      <div class="m-import-manager__header">
        <h2>History</h2>
      </div>
      <res-table class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Time</th>
              <th>Note</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="rev in revisions" :key="rev.id">
              <td><router-link :to="routeTo('parser', rev.id)">{{ formatDateTime(rev.createdAt) }}</router-link></td>
              <td>{{ rev.revisionNote }}</td>
            </tr>
          </tbody>
        </table>
      </res-table>
    </section>
  </main>
</template>

<script>
import { mapActions } from 'vuex';
import { formatDateTime } from '../lib/formatting';
import { formatMutable, mutableHasChanged } from '../lib/utils';
import { parserDefault } from '../store/models/defaults';
import Routing from '../components/mixins/routing';

const mutableSchema = {
  captureType: '',
  groupTaxonomyType: '',
  name: '',
  parserJson: '',
};

export default {
  mixins: [Routing],
  props: {
    id: { type: Number, required: true },
  },
  data() {
    return {
      advanced: false,
      saving: false,
      mutable: formatMutable(mutableSchema),
    };
  },
  computed: {
    parser() {
      return this.$store.state.parserModels[this.id] || parserDefault;
    },
    revisioner() {
      return this.$store.state.parserModels[this.parser.revisionerId] || parserDefault;
    },
    revisions() {
      return this.$store.getters.parserModelsList
        .filter(p => p.revisionerId === this.id)
        .sort((a, b) => b.createdAt.localeCompare(a.createdAt));
    },
    isLoaded() {
      return !!this.parser.id;
    },
    isChanged() {
      return this.isLoaded && mutableHasChanged(this.mutable, this.parser);
    },
    isRevision() {
      return !!this.revisioner.id;
    }
  },
  mounted() {
    this.fetchParser({ id: this.id })
      .then(parser => this.mutable = formatMutable(mutableSchema, parser));
  },
  methods: {
    ...mapActions([
      'fetchParser',
      'setDialog',
      'updateParser',
    ]),
    formatDateTime,
    saveParser(action) {
      if (!this.isChanged || this.saving) return;
      const noteInput = (action !== 'SAVE') ?
        this.setDialog({ tag: 'parser-revision', action }) :
        Promise.resolve(undefined);

      noteInput.then(note => {
        const input = Object.assign({}, this.mutable, { revisionNote: note, saveAction: action });

        // transform empty selection string from form input
        // into a null value for the graphql input...
        // @wishlist: devise a more framework-y way of doing this.
        input.groupTaxonomyType = input.groupTaxonomyType || null;

        this.saving = true;
        this.updateParser({ id: this.id, input })
          .then(parser => {
            this.mutable = formatMutable(mutableSchema, parser);
            this.saving = false;
          })
          .catch(() => this.saving = false);
      });
    }
  }
};
</script>
