<template>
  <tr>
    <td style="width:45%;">
      <span v-if="!editing">{{ mutable.identity }}</span>
      <res-input v-else>
        <input v-model.lazy="mutable.identity" type="text" />
      </res-input>
    </td>
    <td style="width:25%;">
      <span v-if="!editing">{{ mutable.chorusId }}</span>
      <res-input v-else>
        <input v-model.lazy.number="mutable.chorusId" type="text" />
      </res-input>
    </td>
    <td style="width:25%;">{{ model.communityId }}</td>
    <td style="width:5%;">
      <res-button :style="editStyle" paddingless @click.prevent="toggle">
        <res-icon name="edit" size="medium" /><button />
      </res-button>
    </td>
  </tr>
</template>

<script>
import { mapActions } from 'vuex';
import { formatMutable, mutableHasChanged } from '../../lib/utils';

const mutableSchema = {
  chorusId: '',
  identity: '',
};

export default {
  props: {
    model: { type: Object, required: true },
  },
  data() {
    return {
      editing: false,
      mutable: formatMutable(mutableSchema, this.model),
    };
  },
  computed: {
    isChanged() {
      return mutableHasChanged(this.mutable, this.model);
    },
    editStyle() {
      return this.editing ? 'transform:rotate(45deg)' : null;
    }
  },
  methods: {
    ...mapActions([
      'updateIdentity'
    ]),
    save() {
      if (!this.isChanged) return;
      this.updateIdentity({ id: this.model.id, input: this.mutable })
        .then(result => {
          this.mutable = formatMutable(mutableSchema, result);
        });
    },
    toggle() {
      if (this.editing) this.save();
      this.editing = !this.editing;
    }
  }
};
</script>
