<template>
  <main>
    <app-breadcrumbs :project="project" :sitemap="sitemap" :document="document" :capture="capture"/>
    <section>
      <div class="m-import-manager__header">
        <h1>Document Capture: {{ id }}</h1>
      </div>
      <dl v-if="isLoaded" class="m-import-manager__defs">
        <dt>Created</dt>
        <dd>{{ formatDateTime(capture.createdAt) }}</dd>
        <dt>Parser</dt>
        <dd><router-link v-if="parser.id" :to="routeTo('parser', parser.id)">{{ parser.name }}</router-link></dd>
        <dt>Redirect URL</dt>
        <dd>
          <a v-if="capture.redirectUrl" :href="capture.redirectUrl">{{ capture.redirectUrl }}</a>
          <span v-else>N/A</span>
        </dd>
        <dt>Canonical URL</dt>
        <dd><a :href="document.canonicalUrl">{{ document.canonicalUrl }}</a></dd>
        <template v-if="isCurrentImport">
          <dt>Chorus URL</dt>
          <dd><a :href="document.chorusEntryUrl">{{ document.chorusEntryUrl }}</a></dd>
          <dt>Content API UUID</dt>
          <dd>
            <span v-if="document.contentApiUuid">
              <a :href="`/api/content/docs/${ document.contentApiUuid }`">{{ document.contentApiUuid }}</a>
              [<a :href="`/compose/${ document.contentApiUuid }`">editor</a>]
            </span>
            <span v-else>N/A</span>
          </dd>
        </template>
        <dt>Fetch</dt>
        <dd class="--split">
          <span><res-icon :name="processingStatusIcon(capture.fetchStatus)" size="medium" /> {{ capture.fetchStatus }}, http status: {{ capture.httpStatus }}</span>
          <res-button v-if="document.allowsCapture" :disabled="busy" paddingless @click.prevent="processAgain('FETCH')">
            <res-icon name="refresh" size="medium" /> <button>Rerun fetch</button>
          </res-button>
        </dd>
        <dt>Parse</dt>
        <dd class="--split">
          <span><res-icon :name="processingStatusIcon(capture.parseStatus)" size="medium" /> {{ capture.parseStatus }}</span>
          <res-button v-show="isFetched" :disabled="busy" paddingless @click.prevent="processAgain('PARSE')">
            <res-icon name="refresh" size="medium" /> <button>Rerun parse</button>
          </res-button>
        </dd>
        <dt>Import</dt>
        <dd class="--split">
          <span><res-icon :name="processingStatusIcon(capture.importStatus)" size="medium" /> {{ capture.importStatus }}</span>
          <res-button v-show="isParsed" :disabled="busy" paddingless @click.prevent="processAgain('IMPORT')">
            <res-icon name="refresh" size="medium" /> <button>Rerun import</button>
          </res-button>
        </dd>
        <dt>Redirects</dt>
        <dd><res-icon :name="processingStatusIcon(capture.redirectsStatus)" size="medium" /> {{ capture.redirectsStatus }}</dd>
      </dl>
    </section>

    <section v-if="capture.error">
      <div class="m-import-manager__header">
        <h2>Error summary</h2>
      </div>
      <p>{{ capture.errorDigest }}</p>
      <p>
        <res-icon name="x-circle" size="medium"/>
        <strong>{{ capture.error }}:</strong> {{ capture.errorMessage }}
      </p>
      <textarea v-if="errorTrace" :value="errorTrace" class="m-import-manager__console" readonly />
    </section>

    <section v-if="capture.body">
      <div class="m-import-manager__header">
        <h2 id="fetch">Fetched body</h2>
      </div>
      <p>{{ capture.fetchDigest }}</p>
      <textarea :value="capture.body" class="m-import-manager__console" readonly />
    </section>

    <section v-if="capture.storyJson">
      <div class="m-import-manager__header">
        <h2 id="parse">Parsed story</h2>
      </div>
      <p>{{ capture.parseDigest }}</p>
      <textarea :value="prettyStoryJson" class="m-import-manager__console" readonly />
    </section>

    <section v-if="capture.contentApiOps">
      <div class="m-import-manager__header --split">
        <h2 id="content-api-ops">Content API Ops ({{ contentApiOpsTotal }})</h2>
        <res-button paddingless @click.prevent="processAgain('RESET_DELTA')">
          <res-icon name="x" size="medium" /> <button>Reset delta</button>
        </res-button>
      </div>
      <p>{{ capture.contentApiOpsDigest }}</p>
      <textarea :value="prettyContentApiOps" class="m-import-manager__console" readonly />
    </section>
  </main>
</template>

<script>
import AppBreadcrumbs from '../components/app-breadcrumbs.vue';
import Routing from '../components/mixins/routing';
import { mapActions } from 'vuex';
import { formatDateTime, processingStatusIcons } from '../lib/formatting';
import {
  captureDefault,
  documentDefault,
  parserDefault,
  projectDefault,
  sitemapDefault
} from '../store/models/defaults';

export default {
  components: {
    AppBreadcrumbs
  },
  mixins: [Routing],
  props: {
    id: { type: Number, required: true },
  },
  data() {
    return {
      busy: false,
    };
  },
  computed: {
    capture() {
      return this.$store.state.captureModels[this.id] || captureDefault;
    },
    document() {
      return this.$store.state.documentModels[this.capture.documentEntryId] || documentDefault;
    },
    sitemap() {
      return this.$store.state.sitemapModels[this.document.sitemapEntryId] || sitemapDefault;
    },
    project() {
      return this.$store.state.projectModels[this.document.projectId] || projectDefault;
    },
    parser() {
      return this.$store.state.parserModels[this.project.documentParserId] || parserDefault;
    },
    isCurrentImport() {
      return this.id === this.document.importedDocumentCaptureId;
    },
    isLoaded() {
      return !!this.capture.id;
    },
    isFetched() {
      return this.capture.fetchStatus === 'COMPLETE';
    },
    isParsed() {
      return this.capture.parseStatus === 'COMPLETE';
    },
    errorTrace() {
      if (this.capture.errorTrace) {
        try {
          let trace = JSON.parse(this.capture.errorTrace);
          if (Array.isArray(trace)) return trace.join('\n');
        } catch(e) {
          // do nothing
        }
      }
      return this.capture.errorTrace || '';
    },
    prettyStoryJson() {
      const storyData = JSON.parse(this.capture.storyJson);
      return JSON.stringify(storyData, null, 2);
    },
    prettyContentApiOps() {
      const ops = JSON.parse(this.capture.contentApiOps);
      return JSON.stringify(ops, null, 2);
    },
    contentApiOpsTotal() {
      return JSON.parse(this.capture.contentApiOps).length;
    }
  },
  mounted() {
    this.fetchCapture({ id: this.id });
  },
  destroyed() {
    this.unloadCapture({ id: this.id });
  },
  methods: {
    ...mapActions([
      'fetchCapture',
      'processCapture',
      'unloadCapture',
    ]),
    formatDateTime,
    processingStatusIcon(status) {
      return processingStatusIcons[status] || processingStatusIcons.UNATTEMPTED;
    },
    processAgain(action) {
      this.busy = true;
      this.processCapture({ id: this.id, action })
        .then(() => this.busy = false);
    }
  }
};
</script>
