<template>
  <nav class="m-import-manager__breadcrumbs">
    <span v-if="hasProject">
      <router-link :to="routeTo('project', project.id)" title="Project">{{ project.name }}</router-link> /
    </span>
    <span v-else>&nbsp;</span>

    <span v-if="parentSitemap">
      <router-link :to="routeTo('sitemap', parentSitemap.id)" title="Sitemap index">{{ parentSitemap.name }}</router-link> /
    </span>
    <span v-if="hasSitemap && !isSitemap">
      <router-link :to="routeTo('sitemap', sitemap.id)" title="Sitemap urlset">{{ sitemap.name }}</router-link> /
    </span>
    <strong v-else-if="isSitemap">{{ sitemap.name }}</strong>

    <span v-if="hasDocument && !isDocument">
      <router-link :to="routeTo('document', document.id)" title="Document">{{ document.id }}</router-link> /
    </span>
    <strong v-else-if="isDocument">{{ document.id }}</strong>

    <span v-if="hasCapture && !isCapture">
      <router-link :to="routeTo('capture', capture.id)" title="Capture">{{ capture.id }}</router-link> /
    </span>
    <strong v-else-if="isCapture">{{ capture.id }}</strong>
  </nav>
</template>

<script>
import Routing from './mixins/routing';

export default {
  mixins: [Routing],
  props: {
    project: {
      type: Object,
      // default: () => ({})
    },
    sitemap: {
      type: Object,
      // default: () => ({})
    },
    document: {
      type: Object,
      // default: () => ({})
    },
    capture: {
      type: Object,
      // default: () => ({})
    }
  },
  computed: {
    hasProject() {
      return this.project && !!this.project.id;
    },
    hasSitemap() {
      return this.sitemap && !!this.sitemap.id;
    },
    parentSitemap() {
      return this.hasSitemap ? this.$store.state.sitemapModels[this.sitemap.parentId] : null;
    },
    isSitemap() {
      return this.hasSitemap && this.lastObject === 'sitemap';
    },
    hasDocument() {
      return this.document && !!this.document.id;
    },
    isDocument() {
      return this.hasDocument && this.lastObject === 'document';
    },
    hasCapture() {
      return this.capture && !!this.capture.id;
    },
    isCapture() {
      return this.hasCapture && this.lastObject === 'capture';
    },
    lastObject() {
      return ['capture', 'document', 'sitemap', 'project'].find(key => this[key] && !!this[key].id);
    }
  }
};
</script>
