<template>
  <div class="m-import-manager__table">
    <res-table zebra="true" class="m-dashboard__table">
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>{{ (results[0] || {}).allowsCrawling ? 'Crawler' : 'Status' }}</th>
            <th>Documents</th>
            <th>Imported</th>
            <th>Failed</th>
            <th>New</th>
            <th>Archived</th>
            <th>Fetched</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="sitemap in results" :key="sitemap.id">
            <td><router-link :to="routeTo('sitemap', sitemap.id)">{{ sitemap.name }}</router-link></td>
            <td>
              <a v-if="sitemap.queued" title="QUEUED">
                <res-icon name="clock" size="medium" />
              </a>
              <a v-if="sitemap.allowsCrawling" :title="sitemap.crawler ? sitemap.crawler.status : 'UNCRAWLED'">
                <res-icon :name="crawlerIconName(sitemap)" :key="crawlerIconName(sitemap)" size="medium" />
              </a>
              <a v-else :title="sitemap.status">
                <res-icon :name="statusIconName(sitemap.status)" :key="statusIconName(sitemap.status)" :class="sitemap.status.toLowerCase()" size="medium" />
              </a>
              <span v-if="sitemap.allowsCrawling && sitemap.crawler">{{ sitemap.crawler.percentComplete }}%</span>
            </td>
            <td>{{ formatNumber(sitemap.activeDocumentsCount) }}</td>
            <td>{{ formatNumber(sitemap.importedDocumentsCount) }}</td>
            <td>{{ formatNumber(sitemap.failedDocumentsCount) }}</td>
            <td>{{ formatNumber(sitemap.newDocumentsCount) }}</td>
            <td>{{ formatNumber(sitemap.archivedDocumentsCount) }}</td>
            <td :data-convert-to-relative="sitemap.fetchedAt">{{ sitemap.fetchedAt ? formatDateTime(sitemap.fetchedAt) : 'N/A' }}</td>
          </tr>
        </tbody>
      </table>
    </res-table>
    <footer>
      <span v-if="loaded">Found {{ formatNumber(totalCount) }} results</span>
      <res-pagination
        :page="page"
        :count="totalCount"
        :display="perPage"
        indicator="page"
        pagination="text-only"
        navigation="text"
        @change="page=$event.target.page"
      />
    </footer>
  </div>
</template>

<script>
import gql from 'graphql-tag';
import { mapGetters } from 'vuex';
import { formatDateTime, formatNumber, stripURIHost, entryStatusIcons, crawlerIcons } from '../lib/formatting';
import Routing from './mixins/routing';

export default {
  mixins: [Routing],
  props: {
    version: {
      type: Number,
      default: null
    },
    parentId: {
      type: Number,
      default: null
    },
    projectId: {
      type: Number,
      default: null
    },
    status: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      loaded: false,
      page: 1,
      perPage: 25,
      results: [],
      totalCount: 0,
    };
  },
  computed: {
    ...mapGetters([
      'sitemapModelsList',
    ])
  },
  watch: {
    version() {
      this.$apollo.queries.results.refetch();
    },
    sitemapModelsList(current, previous) {
      if (current.length !== previous.length) {
        this.$apollo.queries.results.refetch();
      }
    }
  },
  methods: {
    formatDateTime,
    formatNumber,
    stripURIHost,
    statusIconName(status) {
      return entryStatusIcons[status] || entryStatusIcons.CREATED;
    },
    crawlerIconName(sitemap) {
      if (!sitemap.crawler) return crawlerIcons.NOT_CRAWLED;
      return crawlerIcons[sitemap.crawler.status] || crawlerIcons.NOT_CRAWLED;
    }
  },
  apollo: {
    results: {
      client: 'importer',
      query: gql`
        query SearchSitemaps($input: SearchImporterSitemapEntriesInput!) {
          search: searchImporterSitemapEntries(input: $input) {
            pageNumber
            results {
              activeDocumentsCount
              allowsCrawling
              archivedDocumentsCount
              crawler {
                id
                percentComplete
                status
              }
              failedDocumentsCount
              importedDocumentsCount
              fetchedAt
              id
              name
              newDocumentsCount
              queued
              status
              url
            }
            totalCount
          }
        }
      `,
      variables() {
        const input = {
          pageNumber: this.page,
          parentId: this.parentId,
          perPage: this.perPage,
        };

        if (this.projectId !== null) input.projectId = this.projectId;
        if (this.status !== null) input.status = this.status;
        return { input };
      },
      update(data) {
        this.loaded = true;
        this.page = data.search.pageNumber;
        this.totalCount = data.search.totalCount;
        const results = data.search.results;
        return results;
      }
    }
  }
};
</script>
