<template>
  <form @submit.prevent>
    <p v-if="form.prompt">{{ form.prompt }}</p>
    <p v-if="form.params">
      <input :value="form.params" class="m-import-manager__console" type="text" readonly />
    </p>
    <fieldset v-for="(field, fieldName) in fields" :key="fieldName">
      <res-select v-if="field.options">
        <label>{{ field.label }}
          <a v-if="field.info" :href="field.info" target="_blank"><res-icon name="info" size="small" /></a>
        </label>
        <select :name="fieldName">
          <option v-for="(label, value) in field.options" :key="value" :value="value">{{ label || value }}</option>
        </select>
      </res-select>
      <res-input v-else-if="field.type === 'textarea'">
        <label v-if="field.label">{{ field.label }}</label>
        <textarea :name="fieldName" :placeholder="field.placeholder" />
      </res-input>
      <res-input v-else>
        <label v-if="field.label">{{ field.label }}</label>
        <input :type="field.type || 'text'" :name="fieldName" :placeholder="field.placeholder"/>
      </res-input>
    </fieldset>
    <footer>
      <res-button design="primary" @click.prevent="submit()">
        <button type="submit">{{ form.submit }}</button>
      </res-button>
      <res-button design="secondary" class="m-l-1" @click.prevent="close()">
        <button type="button">Cancel</button>
      </res-button>
    </footer>
  </form>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  computed: {
    ...mapState([
      'currentDialog',
    ]),
    form() {
      return this.currentDialog.form || {};
    },
    fields() {
      return this.form.fields || {};
    }
  },
  methods: {
    ...mapActions([
      'setDialog',
    ]),
    numericInput(input) {
      // non-zero falsey values are undefined...
      // check these in raw form, because they may cast into zeros.
      if (!input && input !== 0) return undefined;
      input = Number(input);
      return Number.isInteger(input) ? Math.max(0, input) : undefined;
    },
    submit() {
      const data = Object.keys(this.fields).reduce((memo, fieldName) => {
        const field = this.fields[fieldName];
        const el = this.$el.querySelector(`[name="${fieldName}"]`);
        if (el) {
          if (field.type === 'number') {
            memo[fieldName] = this.numericInput(el.value);
          } else {
            memo[fieldName] = el.value;
          }
        }
        return memo;
      }, {});

      this.currentDialog.resolve(data);
      this.close();
    },
    close() {
      this.setDialog(null);
    }
  }
};
</script>
