import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';
import { newIdentity } from '../models/factories';

export default {
  async createIdentity(store, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateIdentity($input: CreateImporterMappedIdentityInput!) {
          identity: createImporterMappedIdentity(input: $input) {
            chorusId
            id
            identity
            type
          }
        }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(newIdentity(data.identity));
  },

  async updateIdentity(store, params) {
    // params.id = Int
    // params.input = UpdateImporterMappedIdentityInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateIdentity($id: Int!, $input: UpdateImporterMappedIdentityInput!) {
          identity: updateImporterMappedIdentity(id: $id, input: $input) {
            chorusId
            id
            identity
            type
          }
        }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(newIdentity(data.identity));
  }
};
