import { schemaMerge, schemaFilter, resolveObject } from '../../lib/utils';
import * as defs from './defaults';

const PATCH = { patch: true };

export function newCapture(data, options={}) {
  const model = options.patch ?
    schemaFilter(defs.captureDefault, data) :
    schemaMerge(defs.captureDefault, data);
  if (data.documentEntry) model.documentEntryId = data.documentEntry.id;
  return Object.freeze(model);
}

export function newCrawler(data, options={}) {
  const model = options.patch ?
    schemaFilter(defs.crawlerDefault, data) :
    schemaMerge(defs.crawlerDefault, data);
  if (data.sitemapEntry) model.sitemapEntryId = data.sitemapEntry.id;
  return Object.freeze(model);
}

export function newDocument(data, options={}) {
  const model = options.patch ?
    schemaFilter(defs.documentDefault, data) :
    schemaMerge(defs.documentDefault, data);
  if (data.sitemapEntry) model.sitemapEntryId = data.sitemapEntry.id;
  if (data.project) model.projectId = data.project.id;
  return Object.freeze(model);
}

