export function newSitemap(data, options={}) {
  const model = options.patch ?
    schemaFilter(defs.sitemapDefault, data) :
    schemaMerge(defs.sitemapDefault, data);
  if (data.parentSitemap) model.parentId = data.parentSitemap.id;
  if (data.project) model.projectId = data.project.id;
  if (data.crawler) model.crawlerId = data.crawler.id;
  if (data.currentCrawler) model.currentCrawlerId = data.currentCrawler.id;
  return Object.freeze(model);
}

const factoriesByModelSet = {
  captureModels: newCapture,
  communityModels: obj => obj,
  crawlerModels: newCrawler,
  documentModels: newDocument,
  jobModels: newJob,
  parserModels: newParser,
  placeholderModels: newPlaceholder,
  projectModels: newProject,
  sitemapModels: newSitemap,
};

export function buildModels(mapping, options={}) {
  const models = {};

  for (let [key, value] of Object.entries(mapping)) {
    if (key === 'data') continue;
    const refs = Array.isArray(value) ? value : [value];
    const factory = factoriesByModelSet[key];
    if (!factory) throw 'invalid model namespace';

    for (let obj of refs) {
      if (typeof obj === 'string') {
        if (!mapping.data) throw 'string reference requires a data param';
        obj = resolveObject(mapping.data, obj);
      }
      if (obj) {
        if (!Array.isArray(obj)) obj = [obj];
        models[key] = models[key] || [];
        for (let o of obj) {
          models[key].push(factory(o, options));
        }
      }
    }
  }

  return models;
}

export function buildPatches(mapping) {
  return buildModels(mapping, PATCH);
}
