<template>
  <div class="p-flex--v l-fullheight">
    <app-notifications/>
    <nav class="m-nav m-nav--dashboard">
      <community-select
        v-if="community && currentUser"
        v-model="communityId"
        :community="community"
        :user="currentUser"
        :entitlements="entitlements"
        :allowedRoles="{}"
        class="m-community-select"
      />
      <!-- specifically passing an empty object to the allowedRoles prop so
      the community-select here will not produce a community-menu -->

      <div v-if="accessGranted" class="m-nav__right">
        <res-button
          v-show="!isDashboard"
          class="m-r-1"
          @click.prevent="goToDashboard()"
        >
          <button>Dashboard</button>
        </res-button>
        <res-button
          id="dropdown-manage"
          class="m-r-1"
        >
          <button>Manage</button>
          <res-icon name="down" />
        </res-button>
        <res-dropdown ref="dropdownManage" for="dropdown-manage" @click="closeDropdown($event)">
          <a :href="`${ importManagerUrl() }/sidekiq`" target="_blank">Sidekiq queue</a>
          <router-link :to="routeTo('jobs')">Document jobs</router-link>
          <router-link :to="routeTo('communities')">Communities</router-link>
          <router-link :to="routeTo('placeholders')">Placeholders</router-link>
          <router-link :to="routeTo('asset-identities')">Asset identities</router-link>
          <router-link :to="routeTo('gallery-identities')">Gallery identities</router-link>
          <router-link :to="routeTo('group-identities')">Group identities</router-link>
          <router-link :to="routeTo('user-identities')">User identities</router-link>
        </res-dropdown>

        <res-input ref="quickSearch" class="m-import-manager__quick-search">
          <input v-model.lazy="quickSearch" type="text" placeholder="URL or Error Code" />
        </res-input>
      </div>
    </nav>

    <div class="l-content p-flex is-compose">
      <div id="viewport" class="l-content__inner p-flex--v">
        <router-view v-if="accessGranted" :key="routeKey" class="m-dashboard__content m-import-manager" />
      </div>
    </div>
    <app-modal/>
    <app-loader iconName="more" animation="static" />
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import * as cmd from './store/commands';
import request from '../client/lib/request';
import CommunitySelect from '../shared/vue/community-select/index.vue';
import AppLoader from '../shared/vue/app-loader/app-loader.vue';
import AppNotifications from './components/app-notifications.vue';
import AppModal from './components/modal/app-modal.vue';
import Routing from './components/mixins/routing';
import { importManagerUrl } from './lib/formatting';
import { CurrentUser } from '../shared/vue/queries/currentUser.graphql';
import { CurrentCommunity } from '../shared/vue/queries/currentCommunity.graphql';

export default {
  components: {
    AppNotifications,
    AppModal,
    AppLoader,
    CommunitySelect
  },
  mixins: [Routing],
  data() {
    return {
      entitlements: [],
      loading: true,
      location: null,
      quickSearch: '',
      userIsNetworkAdmin: false,
    };
  },
  computed: {
    communityId() {
      if (this.$route.query.community_id) {
        return parseInt(this.$route.query.community_id);
      } else {
        window.location = '/';
      }
    },
    hasPermission() {
      return (this.entitlements || []).includes('use_importmanager');
    },
    accessGranted() {
      return Boolean(this.userIsNetworkAdmin && this.communityId && this.hasPermission);
    },
    routeKey() {
      return this.$route.name + ':' + this.$route.params.community_id;
    },
    isDashboard() {
      return this.$route.name && this.$route.name === 'dashboard';
    }
  },
  watch: {
    loading() {
      if (this.loading) {
        this.$store.commit('appLoader/SET_LOADING', true);
      } else {
        this.$store.commit('appLoader/SET_LOADING', false);
      }
    },
    accessGranted() {
      if (this.accessGranted) {
        this.loading = false;
      } else {
        // keep the loading overlay on until all access conditions are met
        this.loading = true;
      }
    },
    quickSearch() {
      if (!this.quickSearch) return;
      this.$refs.quickSearch.classList.remove('--nope');

      this.searchDocuments({ findBy: this.quickSearch })
        .then(search => {
          if (search.results.length > 1) {
            this.$router.replace({ name: 'dashboard', query: { community_id: this.community.id, q: this.quickSearch } });
          } else if (search.results.length === 1) {
            this.$router.replace(this.routeTo('document', search.results[0].id));
          } else {
            this.$refs.quickSearch.classList.add('--nope');
          }
          this.quickSearch = '';
        });
    }
  },
  apollo: {
    currentUser: {
      query: CurrentUser,
      result({ data }) {
        if (data.currentUser.networkRoles.find(role => role === 'NETWORK_ADMIN')) {
          this.userIsNetworkAdmin = true;
          this.$store.commit(cmd.SET_CONTEXT, { user: data.currentUser });
        } else {
          // only Network Admins should have access to the importmanager,
          // if the current user is not a network admin, redirect them to the root
          window.location = '/';
        }
      }
    },
    community: {
      query() {
        return CurrentCommunity;
      },
      result({ data }) {
        this.$store.commit(cmd.SET_CONTEXT, { community: data.community });
        if (data.community.id) {
          this.getEntitlements();
        } else {
          // must have a community ID to work with,
          // if the community ID is missing, redirect the user to the root
          window.location = '/';
        }
      },
      variables() {
        return {
          id: this.communityId
        };
      }
    }
  },
  methods: {
    ...mapActions([
      'searchDocuments'
    ]),
    importManagerUrl,
    goToDashboard() {
      this.$router.replace(this.routeTo('dashboard'));
    },
    closeDropdown(evt) {
      let el = evt.target;
      while (el && el.tagName) {
        if (/res-dropdown/i.test(el.tagName)) {
          el.toggle();
          break;
        }
        el = el.parentNode;
      }
    },
    async getEntitlements() {
      this.entitlements = await request(`/api/entitlements?communityId=${this.communityId}`);
      this.$store.commit(cmd.SET_CONTEXT, { entitlements: this.entitlements });
      // if the entitlement of 'use_importmanager' is missing, redirect the user to the root
      if (!this.entitlements.includes('use_importmanager')) window.location = '/';
    },
    pushRoute(name) {
      const route = Object.assign({}, this.$route);
      route.query = Object.assign({}, this.$route.query, { community_id: this.communityId });
      if (name) route.name = name;
      this.$router.replace(route);
    },
  }
};
</script>
