import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

const JobBaseAttrs = gql`
  fragment JobBaseAttrs on ImporterDocumentJob {
    createdAt
    documentsTotal
    documentsComplete
    finishAt
    id
    jobId
    lastDocumentId
    percentComplete
    searchJson
    status
    startAt
    task
  }
`;

export default {
  async fetchJobTasks({ state }) {
    if (!state.documentJobTasks) {
      const { data } = await importerClient.query({
        query: gql`
          query FetchJobTasks($communityId: Int!) {
            tasks: importerDocumentJobTasks(communityId: $communityId)
          }
        `,
        variables: {
          communityId: state.currentCommunity.id
        }
      }).catch(err => this.commit(cmd.PUT_NOTICE, err));

      this.commit(cmd.SET_TASKS, data.tasks);
    }
    return Promise.resolve(state.documentJobTasks);
  },

  async fetchJobs({ state }) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchJobs($communityId: Int!) {
          jobs: importerDocumentJobs(communityId: $communityId) {
            ...JobBaseAttrs
            errorMessage
          }
        }
        ${ JobBaseAttrs }
      `,
      variables: {
        communityId: state.currentCommunity.id
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, { jobModels: data.jobs });
    return Promise.resolve();
  },

  async createJob(store, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateJob($input: CreateImporterDocumentJobInput!) {
          jobs: createImporterDocumentJob(input: $input) {
            ...JobBaseAttrs
          }
        }
        ${ JobBaseAttrs }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, { jobModels: data.jobs });
    return Promise.resolve(data.jobs[0].jobId);
  },

  async cancelJob(store, ref) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CancelJob($id: ID!) {
          jobs: cancelImporterDocumentJob(id: $id) {
            ...JobBaseAttrs
          }
        }
        ${ JobBaseAttrs }
      `,
      variables: {
        id: String(ref)
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { jobModels: data.jobs });
    return Promise.resolve();
  },

  async resumeJob(store, ref) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation ResumeJob($id: ID!) {
          jobs: resumeImporterDocumentJob(id: $id) {
            ...JobBaseAttrs
          }
        }
        ${ JobBaseAttrs }
      `,
      variables: {
        id: String(ref)
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { jobModels: data.jobs });
    return Promise.resolve();
  },

  async deleteJob(store, ref) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation DeleteJob($id: ID!) {
          jobs: deleteImporterDocumentJob(id: $id) {
            id
          }
        }
      `,
      variables: {
        id: String(ref)
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.DELETE_MODELS, { jobModels: data.jobs });
    return Promise.resolve();
  }
};
