<template>
  <main>
    <div class="m-import-manager__header --split">
      <h1>Communities</h1>
      <res-button paddingless @click.prevent="add">
        <res-icon name="add" size="medium" /> <button>New community</button>
      </res-button>
    </div>

    <div class="m-import-manager__table">
      <res-table zebra="true" class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Community name</th>
              <th>Community ID</th>
              <th colspan="2">URL matcher</th>
            </tr>
          </thead>
          <tbody>
            <communities-row v-for="result in results" :key="result.id" :model="result"/>
          </tbody>
        </table>
      </res-table>
      <footer v-if="loaded">
        <span v-if="results.length">Found {{ results.length }} results</span>
        <span v-else>No results</span>
      </footer>
    </div>
  </main>
</template>

<script>
import gql from 'graphql-tag';
import { mapState, mapActions } from 'vuex';
import CommunitiesRow from '../components/tables/communities-row.vue';

const createForm = {
  fields: {
    name: {
      label: 'Community name',
      type: 'text'
    },
    constituentCommunityId: {
      label: 'Community ID',
      type: 'number'
    }
  },
  submit: 'Create Community Delegation'
};

export default {
  components: {
    CommunitiesRow
  },
  data() {
    return {
      loaded: false,
      results: []
    };
  },
  computed: {
    ...mapState(['currentCommunity', 'network'])
  },
  methods: {
    ...mapActions(['setDialog']),
    add() {
      this.setDialog({ tag: 'input-form', form: createForm })
        .then(async (data) => {
          if (!data.name || !data.constituentCommunityId) return;
          await this.$store.dispatch('createCommunityDelegation', {
            constituentCommunityId: data.constituentCommunityId,
            delegateCommunityId: this.currentCommunity.id,
            networkId: this.network.id,
            name: data.name,
          });
          this.$apollo.queries.results.refresh();
        });
    }
  },
  apollo: {
    results: {
      client: 'importer',
      query: gql`
        query FetchCommunityDelegations($delegateId: Int!) {
          results: importerCommunityDelegations(delegateId: $delegateId) {
            constituentCommunityId
            delegateCommunityId
            id
            name
            urlMatcher
          }
        }
      `,
      variables() {
        return {
          delegateId: this.currentCommunity.id,
        };
      },
      update(data) {
        this.loaded = true;
        return data.results;
      }
    }
  }
};
</script>
