import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

export const CrawlerBaseAttrs = gql`
  fragment CrawlerBaseAttrs on ImporterSitemapCrawler {
    autoAdvance
    documentsComplete
    documentsLimit
    documentsTotal
    duration
    finishedAt
    id
    percentComplete
    recordsPerSecond
    sitemapEntryId
    sitemapsComplete
    sitemapsLimit
    sitemapsTotal
    status
  }
`;

export default {
  async createCrawler({ state }, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateCrawler($input: CreateImporterSitemapCrawlerInput!) {
          crawler: createImporterSitemapCrawler(input: $input) {
            ...CrawlerBaseAttrs
            sitemapEntry {
              id
              name
              crawler {
                id
              }
              currentCrawler {
                id
              }
            }
          }
        }
        ${ CrawlerBaseAttrs }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, {
      crawlerModels: data.crawler,
      sitemapModels: data.crawler.sitemapEntry,
    });
    return Promise.resolve(state.crawlerModels[data.crawler.id]);
  },

  async updateCrawler({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateCrawler($input: UpdateImporterSitemapCrawlerInput!, $id: Int!) {
          crawler: updateImporterSitemapCrawler(input: $input, id: $id) {
            ...CrawlerBaseAttrs
          }
        }
        ${ CrawlerBaseAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, { crawlerModels: data.crawler });
    return Promise.resolve(state.crawlerModels[params.id]);
  },

  async stopCrawler({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation StopCrawler($id: Int!) {
          crawler: stopImporterSitemapCrawler(id: $id) {
            ...CrawlerBaseAttrs
          }
        }
        ${ CrawlerBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, { crawlerModels: data.crawler });
    return Promise.resolve(state.crawlerModels[params.id]);
  }
};
