export default {
  data() {
    return {
      _pollId: null,
    };
  },
  methods: {
    startPolling(handler, interval) {
      this.stopPolling();
      this._pollId = setInterval(handler, interval);
    },
    startImmediatePolling(handler, interval) {
      this.startPolling(handler, interval);
      handler();
    },
    stopPolling() {
      if (this._pollId) {
        clearInterval(this._pollId);
        this._pollId = null;
      }
    }
  },
  beforeDestroy() {
    this.stopPolling();
  }
};
