import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

const ParserBaseAttrs = gql`
  fragment ParserBaseAttrs on ImporterDocumentParser {
    createdAt
    id
    name
    revisionerId
  }
`;

export default {

  async fetchParser({ state }, params) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchParser($id: Int!) {
          parser: importerDocumentParser(id: $id) {
            ...ParserBaseAttrs
            captureType
            captureTypes
            groupTaxonomyType
            groupTaxonomyTypes
            parserJson
            revisioner {
              ...ParserBaseAttrs
            }
            revisions {
              ...ParserBaseAttrs
              revisionNote
            }
            schemaJson
          }
        }
        ${ ParserBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, {
      parserModels: [
        data.parser,
        data.parser.revisioner,
      ].concat(data.parser.revisions)
    });
    return Promise.resolve(state.parserModels[data.parser.id]);
  },

  async createParser({ state }, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateParser($input: CreateImporterDocumentParserInput!) {
          parser: createImporterDocumentParser(input: $input) {
            ...ParserBaseAttrs
          }
        }
        ${ ParserBaseAttrs }
      `,
      variables: {
        input: Object.assign({
          communityId: state.currentCommunity.id,
        }, input)
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { parserModels: data.parser });
    return Promise.resolve(state.parserModels[data.parser.id]);
  },

  async updateParser({ state }, params) {
    // params.id = Int
    // params.input = UpdateImporterDocumentParserInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateParser($id: Int!, $input: UpdateImporterDocumentParserInput!) {
          parser: updateImporterDocumentParser(id: $id, input: $input) {
            ...ParserBaseAttrs
            captureType
            groupTaxonomyType
            parserJson
            revisions {
              ...ParserBaseAttrs
              revisionNote
            }
          }
        }
        ${ ParserBaseAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    if (data.errors) return Promise.reject(data.errors);

    this.commit(cmd.PATCH_MODELS, { parserModels: [data.parser].concat(data.parser.revisions) });
    return Promise.resolve(state.parserModels[params.id]);
  }
};
