<template>
  <div v-if="crawlable || hasRunningCrawler" :class="{ '--running': hasRunningCrawler }" class="m-import-manager__crawler">
    <header>
      <template v-if="hasRunningCrawler">
        <strong>
          Crawler running in <router-link :to="routeTo('sitemap', crawlerSitemap.id)">{{ crawlerSitemap.name }}</router-link>...
          {{ crawler.percentComplete }}% complete
        </strong>
        <div>
          <res-checkbox v-if="crawlerSitemap.parentId">
            <input v-model="autoAdvance" type="checkbox" /> <label>auto-advance</label>
          </res-checkbox>
          <res-button key="stop-crawling" class="m-l-5" paddingless @click.prevent="cancelCrawler()">
            <res-icon name="none" size="medium" /> <button>Stop crawling</button>
          </res-button>
        </div>
      </template>

      <template v-else-if="crawlable && hasCrawlerLock">
        <strong>Crawling is disabled for this project</strong>
      </template>

      <template v-else-if="crawlable && hasIdleCrawler">
        <strong>Last crawl of {{ sitemap.name }} {{ String(crawler.status).toLowerCase() }}</strong>
        <button type="button" title="Start new crawler" paddingless @click.prevent="startCrawler()">
          <res-icon name="amp" size="medium"/> <span>Start new crawler</span>
        </button>
      </template>

      <template v-else-if="crawlable && !hasCrawler">
        <strong>No crawler history</strong>
        <res-button key="start-crawling" paddingless @click.prevent="startCrawler()">
          <res-icon name="amp" size="medium" /> <button>Start crawling</button>
        </res-button>
      </template>
    </header>

    <div v-show="crawlable && hasCrawler && !hasCrawlerLock" class="m-import-manager__crawler__stats">
      <p v-if="hasRunningCrawler">Running for {{ formatHMS(crawler.duration) }}, now at ~{{ Math.round(crawler.recordsPerSecond * 100) / 100 }} documents per second</p>
      <p v-else>Finished {{ formatDateTime(crawler.finishedAt) }}, took {{ formatHMS(crawler.duration) }}</p>
      <p>
        <res-icon :name="statusIcon(crawler.sitemapsTotal, crawler.sitemapsComplete)" size="small"/>
        found {{ crawler.sitemapsTotal }} sitemaps, fetched {{ crawler.sitemapsComplete }} <span v-show="crawler.sitemapsLimit">(limit {{ crawler.sitemapsLimit+1 }})</span>
      </p>
      <p>
        <res-icon :name="statusIcon(crawler.documentsTotal, crawler.documentsComplete)" size="small"/>
        found {{ crawler.documentsTotal }} documents, fetched {{ crawler.documentsComplete }} <span v-show="crawler.documentsLimit">(limit {{ crawler.documentsLimit }})</span>
      </p>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import { formatDateTime, formatHMS } from '../lib/formatting';
import { crawlerDefault, sitemapDefault } from '../store/models/defaults';
import Routing from './mixins/routing';

const createCrawlerForm = {
  prompt: 'Are you sure you want to start crawling this sitemap? This may take a while.',
  fields: {
    sitemapsLimit: {
      label: 'Sitemaps limit',
      placeholder: 'all',
      type: 'number'
    },
    documentsLimit: {
      label: 'Documents per sitemap',
      placeholder: 'all',
      type: 'number'
    },
    captureMode: {
      label: 'Capture mode',
      options: {
        DEFAULT: 'Capture missing/failed (default)',
        FORCE_RECAPTURE: 'Capture all (force recapture)',
        TAXONOMY_ONLY: 'Capture none (taxonomy only)',
      }
    }
  },
  submit: 'Start Crawler'
};

export default {
  mixins: [Routing],
  props: {
    id: {
      type: Number,
      default: null
    },
    sitemapId: {
      type: Number,
      default: null
    },
    crawlable: { type: Boolean, default: false },
  },
  data() {
    const crawler = this.$store.state.crawlerModels[this.id] || crawlerDefault;
    return { autoAdvance: crawler.autoAdvance };
  },
  computed: {
    crawler() {
      return this.$store.state.crawlerModels[this.id] || crawlerDefault;
    },
    crawlerSitemap() {
      return this.$store.state.sitemapModels[this.crawler.sitemapEntryId] || sitemapDefault;
    },
    sitemap() {
      return this.$store.state.sitemapModels[this.sitemapId] || sitemapDefault;
    },
    hasCrawler() {
      return !!this.crawler.id;
    },
    hasCrawlerLock() {
      return !this.sitemap.allowsCrawling;
    },
    hasIdleCrawler() {
      return this.hasCrawler && this.crawler.status !== 'RUNNING';
    },
    hasRunningCrawler() {
      return this.hasCrawler && this.crawler.status === 'RUNNING';
    },
    mayCrawl() {
      return !this.hasCrawler || !this.hasRunningCrawler;
    }
  },
  watch: {
    autoAdvance() {
      this.updateCrawler({ id: this.id, input: { autoAdvance: this.autoAdvance } })
        .then(crawler => this.autoAdvance = crawler.autoAdvance);
    }
  },
  methods: {
    ...mapActions([
      'createCrawler',
      'updateCrawler',
      'setDialog',
      'stopCrawler',
    ]),
    formatHMS,
    formatDateTime,
    statusIcon(total, complete) {
      if (total > 0 && total === complete) return 'check-circle';
      if (this.hasRunningCrawler) return 'loading';
      return 'minus-circle';
    },
    startCrawler() {
      this.setDialog({ tag: 'input-form', form: createCrawlerForm })
        .then(data => {
          this.createCrawler({
            captureMode: data.captureMode,
            documentsLimit: data.documentsLimit,
            sitemapsLimit: data.sitemapsLimit,
            sitemapEntryId: this.sitemapId,
          });
        });
    },
    cancelCrawler() {
      if (!this.hasRunningCrawler) return;
      if (confirm('Stop crawling?')) {
        this.stopCrawler({ id: this.id });
      }
    }
  }
};
</script>
