<template>
  <main>
    <app-breadcrumbs :project="project" :sitemap="sitemap" :document="document"/>
    <section>
      <div class="m-import-manager__header">
        <h1>Document: {{ id }}</h1>
      </div>
      <dl class="m-import-manager__defs">
        <dt>Status</dt>
        <dd><res-icon :name="statusIcon" size="medium" /> {{ document.status }}</dd>
        <dt>Archival</dt>
        <dd class="--split">
          <span><res-icon :name="archivalStatusIcon" size="medium" /> {{ document.archivalStatus }}</span>
          <res-button :disabled="saving" paddingless @click.prevent="toggleArchived()">
            <res-icon name="replace" size="medium" /> <button>{{ document.archived ? 'Unarchive' : 'Archive' }}</button>
          </res-button>
        </dd>
        <dt>Starred</dt>
        <dd :class="{ 'is-watched': document.watched }" class="--split">
          <span><res-icon name="star" size="medium" /> {{ document.watched ? 'YES' : 'NO' }}</span>
          <res-button :disabled="saving" paddingless @click.prevent="toggleWatched">
            <res-icon name="replace" size="medium" /> <button>{{ document.watched ? 'Unstar' : 'Star' }}</button>
          </res-button>
        </dd>
        <dt>Community</dt>
        <dd class="--split">
          <span>{{ communityNameById[document.communityId] }}</span>
          <res-button :disabled="saving" paddingless @click.prevent="changeCommunity">
            <res-icon name="replace" size="medium" /> <button>Change</button>
          </res-button>
        </dd>
        <dt>Canonical URL</dt>
        <dd><a :href="document.canonicalUrl">{{ document.canonicalUrl }}</a></dd>
        <dt>Chorus URL</dt>
        <dd>
          <a v-if="document.chorusEntryUrl" :href="document.chorusEntryUrl">{{ document.chorusEntryUrl }}</a>
          <span v-else>N/A</span>
        </dd>
        <dt>SBN entry ID</dt>
        <dd>{{ document.chorusEntryId || 'N/A' }}</dd>
        <dt>Content API UUID</dt>
        <dd>
          <span v-if="document.contentApiUuid">
            <a :href="`/api/content/docs/${ document.contentApiUuid }`">{{ document.contentApiUuid }}</a>
            | <a :href="`/compose/${ document.contentApiUuid }`">Open in story editor</a>
          </span>
          <span v-else>N/A</span>
        </dd>
        <dt>Chorus API</dt>
        <dd>
          <span v-if="document.towerUrl">
            <a :href="document.towerUrl">Open in Chorus API</a>
          </span>
          <span v-else>N/A</span>
        </dd>
        <dt v-if="document.clickabilityUrl">Clickability URL</dt>
        <dd v-if="document.clickabilityUrl">
          <span>
            <a :href="document.clickabilityUrl">{{ document.clickabilityUrl }}</a>
          </span>
        </dd>
        <dt>Last fetched</dt>
        <dd>{{ document.fetchedAt ? formatDateTime(document.fetchedAt) : 'N/A' }}</dd>
        <dt>Last modified</dt>
        <dd>{{ formatDateTime(document.modifiedAt) }}</dd>
      </dl>
    </section>

    <section v-if="placeholders.length" class="m-import-manager__table">
      <div class="m-import-manager__header">
        <h2>Placeholders</h2>
      </div>
      <res-table class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Type</th>
              <th>Data</th>
              <th>Fulfilled</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="placeholder in placeholders" :key="placeholder.id">
              <td>{{ placeholder.id }}</td>
              <td>{{ placeholder.contentType }}</td>
              <td>
                <span v-if="placeholder.data.length <= 40">{{ placeholder.data }}</span>
                <a v-else href="#" class="m-clip-text" @click.prevent="setDialog({ tag: 'text-console', value: placeholder.data })">{{ placeholder.data }}</a>
              </td>
              <td>
                <abbr :title="placeholder.fulfilled ? 'FULFILLED' : 'UNFULFILLED'">
                  <res-icon :name="placeholder.fulfilled ? 'check-circle' : 'minus-circle'" size="medium" />
                </abbr>
              </td>
            </tr>
          </tbody>
        </table>
      </res-table>
    </section>

    <section class="m-import-manager__table">
      <div class="m-import-manager__header">
        <h2>History</h2>
      </div>
      <res-table class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Time</th>
              <th>Fetch</th>
              <th>Parse</th>
              <th>Imported</th>
              <th colspan="2">Redirects</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="capture in captures" :key="capture.id">
              <td><router-link :to="routeTo('capture', capture.id)">{{ formatDateTime(capture.createdAt) }}</router-link></td>
              <td>
                <a :title="capture.fetchStatus">
                  <res-icon :name="processingStatusIcon(capture.fetchStatus)" size="medium"/>
                </a>
                <router-link
                  v-if="capture.fetchDigest"
                  :to="routeTo('capture', capture.id)"
                  class="m-import-manager__digest"
                >
                  {{ capture.fetchDigest.slice(0, 8) }}
                </router-link>
              </td>
              <td>
                <a :title="capture.parseStatus">
                  <res-icon :name="processingStatusIcon(capture.parseStatus)" size="medium"/>
                </a>
                <router-link
                  v-if="capture.parseDigest"
                  :to="routeTo('capture', capture.id)"
                  class="m-import-manager__digest"
                >
                  {{ capture.parseDigest.slice(0, 8) }}
                </router-link>
              </td>
              <td>
                <a :title="capture.importStatus">
                  <res-icon :name="processingStatusIcon(capture.importStatus)" size="medium"/>
                </a>
                <router-link
                  v-if="capture.contentApiOpsDigest"
                  :to="routeTo('capture', capture.id)"
                  class="m-import-manager__digest"
                >
                  {{ capture.contentApiOpsDigest.slice(0, 8) }}
                </router-link>
              </td>
              <td>
                <a :title="capture.redirectsStatus">
                  <res-icon :name="processingStatusIcon(capture.redirectsStatus)" size="medium" />
                </a>
              </td>
              <td>
                <template v-if="capture.fetchStatus === 'COMPLETE'">
                  <res-button :id="'capture-actions-'+capture.id" paddingless>
                    <res-icon class="more" name="more" animated="false" /><button />
                  </res-button>
                  <res-dropdown :for="'capture-actions-'+capture.id" placement="left">
                    <button
                      :disabled="!!processing[capture.id]"
                      type="button"
                      title="Rerun parse"
                      paddingless
                      @click.prevent="onReprocessCapture($event, capture.id, 'PARSE')"
                    >
                      <res-icon name="grid" size="medium"/> <span>Rerun parse</span>
                    </button>
                    <button
                      :disabled="!!processing[capture.id]"
                      type="button"
                      title="Rerun import"
                      paddingless
                      @click.prevent="onReprocessCapture($event, capture.id, 'IMPORT')"
                    >
                      <res-icon name="storystream" size="medium" /> <span>Rerun import</span>
                    </button>
                  </res-dropdown>
                </template>
              </td>
            </tr>
          </tbody>
        </table>
      </res-table>
      <footer>
        <span>Captured {{ captures.length }} time{{ captures.length !== 1 ? 's' : '' }}</span>
        <res-button v-if="document.allowsCapture" :disabled="capturing" design="secondary" @click.prevent="onCapture()">
          <res-icon :name="capturing ? 'loading' : 'refresh'" size="medium"/>
          <button>{{ captures.length ? 'New capture' : 'Capture now' }}</button>
        </res-button>
      </footer>
    </section>
  </main>
</template>

<script>
import Polling from '../components/mixins/polling';
import Routing from '../components/mixins/routing';
import AppBreadcrumbs from '../components/app-breadcrumbs.vue';
import { mapActions, mapGetters } from 'vuex';
import { formatMutable } from '../lib/utils';
import { formatDateTime, entryStatusIcons, processingStatusIcons } from '../lib/formatting';
import {
  documentDefault,
  projectDefault,
  sitemapDefault
} from '../store/models/defaults';

const changeCommunityForm = {
  prompt: 'The document will be deleted from its current community and republish here.',
  fields: {
    communityId: {
      label: 'Community',
      options: {}
    }
  },
  submit: 'Change Community'
};

const mutableSchema = {
  archivalStatus: null,
  watched: null,
};

export default {
  components: {
    AppBreadcrumbs,
  },
  mixins: [Polling, Routing],
  props: {
    id: { type: Number, required: true },
  },
  data() {
    return {
      capturing: false,
      mutable: formatMutable(mutableSchema),
      processing: {},
      saving: false,
    };
  },
  computed: {
    ...mapGetters(['communityNameById']),
    document() {
      return this.$store.state.documentModels[this.id] || documentDefault;
    },
    captures() {
      return Object.values(this.$store.state.captureModels)
        .filter(c => c.documentEntryId === this.id)
        .sort((a, b) => String(b.createdAt).localeCompare(a.createdAt));
    },
    placeholders() {
      return Object.values(this.$store.state.placeholderModels)
        .filter(c => c.documentEntryId === this.id);
    },
    sitemap() {
      return this.$store.state.sitemapModels[this.document.sitemapEntryId] || sitemapDefault;
    },
    project() {
      return this.$store.state.projectModels[this.document.projectId] || projectDefault;
    },
    statusIcon() {
      return entryStatusIcons[this.document.status] || entryStatusIcons.CREATED;
    },
    archivalStatusIcon() {
      return this.document.archived ? 'none' : 'check-circle';
    },
    isLoaded() {
      return !!this.document.id;
    }
  },
  mounted() {
    this.startImmediatePolling(() => {
      this.fetchDocument({ id: this.id })
        .then(doc => this.mutable = formatMutable(mutableSchema, doc));
    }, 15000);
  },
  destroyed() {
    this.unloadDocument({ id: this.id });
  },
  methods: {
    ...mapActions([
      'captureDocument',
      'changeDocumentCommunity',
      'fetchDocument',
      'processCapture',
      'setDialog',
      'unloadDocument',
      'updateDocument',
    ]),
    formatDateTime,
    processingStatusIcon(status) {
      return processingStatusIcons[status] || processingStatusIcons.UNATTEMPTED;
    },
    toggleArchived() {
      if (this.saving) return;
      this.saving = true;
      this.updateDocument({ id: this.id, input: {
        archivalStatus: this.document.archived ? 'ACTIVE' : 'ARCHIVED_BY_ADMIN'
      } })
        .then(doc => {
          this.mutable = formatMutable(mutableSchema, doc);
          this.saving = false;
        });
    },
    toggleWatched() {
      if (this.saving) return;
      this.saving = true;
      this.updateDocument({ id: this.id, input: {
        watched: !this.mutable.watched
      } })
        .then(doc => {
          this.mutable = formatMutable(mutableSchema, doc);
          this.saving = false;
        });
    },
    changeCommunity() {
      const options = Object.assign({}, this.communityNameById);
      delete options[this.document.communityId];

      changeCommunityForm.fields.communityId.options = options;

      this.setDialog({ tag: 'input-form', form: changeCommunityForm })
        .then(async (data) => {
          this.saving = true;
          await this.changeDocumentCommunity({ id: this.id, communityId: Number(data.communityId) });
          this.saving = false;
        });
    },
    onCapture() {
      setTimeout(() => {
        // use timeout so that dropdown can close before ui-blocking confirm runs.
        if (confirm('This will re-fetch the remote document and attempt a fresh import. Proceed?')) {
          this.capturing = true;
          this.captureDocument({ id: this.id })
            .then(() => this.capturing = false);
        }
      }, 0);
    },
    onReprocessCapture(evt, id, action) {
      this.closeDropdown(evt);
      if (this.processing[id]) return;

      // mark capture as processing while waiting for response...
      this.processing = Object.assign({}, this.processing, { [id]: action });
      this.processCapture({ id, action })
        .then(() => this.processing = Object.assign({}, this.processing, { [id]: null }));
    },
    closeDropdown(evt) {
      let el = evt.target;
      while (el && el.tagName) {
        if (/res-dropdown/i.test(el.tagName)) return el.toggle();
        el = el.parentNode;
      }
    }
  }
};
</script>
