import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';
import { CaptureBaseAttrs } from './captures';

const DocumentBaseAttrs = gql`
  fragment DocumentBaseAttrs on ImporterDocumentEntry {
    archived
    archivalStatus
    canonicalUrl
    chorusEntryId
    chorusEntryUrl
    clickabilityUrl
    fetchedAt
    id
    modifiedAt
    status
    towerUrl
    watched
  }
`;

export default {

  async fetchDocument({ state }, params) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchDocument($id: Int!) {
          document: importerDocumentEntry(id: $id) {
            ...DocumentBaseAttrs
            allowsCapture
            communityId
            contentApiUuid
            documentCaptures {
              ...CaptureBaseAttrs
            }
            importedDocumentCaptureId
            project {
              id
              name
              communityDelegations {
                constituentCommunityId
                id
                name
              }
            }
            sitemapEntry {
              id
              name
              parentSitemap {
                id
                name
              }
            }
            storyPlaceholders {
              contentType
              data
              documentEntryId
              id
              fulfilled
            }
          }
        }
        ${ DocumentBaseAttrs }
        ${ CaptureBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, {
      data,
      documentModels: 'document',
      captureModels: 'document.documentCaptures',
      placeholderModels: 'document.storyPlaceholders',
      sitemapModels: ['document.sitemapEntry', 'document.sitemapEntry.parentSitemap'],
      projectModels: 'document.project',
      communityModels: 'document.project.communityDelegations',
    });
    return Promise.resolve(state.documentModels[params.id]);
  },

  async findOrCreateDocument({ state }, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation FindOrCreateDocument($input: CreateImporterDocumentEntryInput!) {
          document: findOrCreateImporterDocumentEntry(input: $input) {
            id
          }
        }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { documentModels: data.document });
    return Promise.resolve(state.documentModels[data.document.id]);
  },

  async updateDocument({ state }, params) {
    // params.id = Int
    // params.input = UpdateImporterDocumentEntryInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateDocument($id: Int!, $input: UpdateImporterDocumentEntryInput!) {
          document: updateImporterDocumentEntry(id: $id, input: $input) {
            archived
            archivalStatus
            id
            watched
          }
        }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { documentModels: data.document });
    return Promise.resolve(state.documentModels[params.id]);
  },

  async captureDocument({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CaptureDocument($id: Int!) {
          document: captureImporterDocumentEntry(id: $id) {
            documentCaptures {
              ...CaptureBaseAttrs
            }
            id
            status
          }
        }
        ${ CaptureBaseAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { documentModels: data.document });
    this.commit(cmd.SET_MODELS, { captureModels: data.document.documentCaptures });
    return Promise.resolve(state.documentModels[params.id]);
  },

  async changeDocumentCommunity({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation ChangeDocumentCommunity($id: Int!, $communityId: Int!) {
          document: changeImporterDocumentEntryCommunity(id: $id, communityId: $communityId) {
            communityId
            contentApiUuid
            chorusEntryId
            chorusEntryUrl
            id
            modifiedAt
          }
        }
      `,
      variables: {
        id: params.id,
        communityId: params.communityId,
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { documentModels: data.document });
    return Promise.resolve(state.documentModels[params.id]);
  },

  unloadDocument({ getters }, params) {
    this.commit(cmd.DELETE_MODELS, {
      captureModels: getters.captureModelsList.filter(d => d.documentEntryId === params.id),
      documentModels: getters.documentModelsList.filter(d => d.id === params.id),
      placeholderModels: getters.placeholderModelsList.filter(d => d.documentEntryId === params.id),
    });
  }
};
