<template>
  <main>
    <div class="m-import-manager__header">
      <h1>Placeholders</h1>
    </div>

    <form class="m-import-manager__fieldset" @submit.prevent>
      <res-input>
        <label>Lookup</label>
        <input v-model.lazy.trim="lookupInput" type="text" placeholder="document ID or content type" />
        <res-icon slot="inner-right" name="search" />
      </res-input>
      <res-select class="m-l-1">
        <label>Filter</label>
        <select v-model="filterInput">
          <option v-for="(value, label) in filterOptions" :key="label" :value="label">{{ label }}</option>
        </select>
      </res-select>
    </form>

    <div class="m-import-manager__table">
      <res-table zebra="true" class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Document</th>
              <th>Type</th>
              <th>Data</th>
              <th>Fulfilled</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="result in results" :key="result.id">
              <td>{{ result.id }}</td>
              <td>
                <router-link :to="routeTo('document', result.documentEntryId)">
                  <res-icon name="versions" size="medium" class="m-r-5" />
                </router-link>
              </td>
              <td>{{ result.contentType }}</td>
              <td>{{ result.data }}</td>
              <td style="min-width:125px;">
                <abbr :title="result.fulfilled ? 'FULFILLED' : 'UNFULFILLED'">
                  <res-icon :name="result.fulfilled ? 'check-circle' : 'minus-circle'" size="medium" />
                </abbr>
                <span v-if="result.fulfilled" class="m-l-5">{{ result.fulfilledType }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </res-table>
      <footer>
        <span v-if="loaded">
          Found {{ formatNumber(totalCount) }} results
          <span v-show="totalCount > 0">
            [<a :href="downloadUrl">download</a> / <a :href="downloadUrl" @click.prevent="runJob">job</a>]
          </span>
        </span>
        <res-pagination
          :page="page"
          :count="totalCount"
          :display="perPage"
          indicator="page"
          pagination="text-only"
          navigation="text"
          @change="page=$event.target.page"
        />
      </footer>
    </div>
  </main>
</template>

<script>
import gql from 'graphql-tag';
import { mapState, mapActions } from 'vuex';
import { formatNumber, importManagerUrl } from '../lib/formatting';
import Routing from '../components/mixins/routing';

const filterOptions = {
  'All': {},
  'Unfulfilled': { fulfilled: false },
  'Fulfilled - all': { fulfilled: true },
  'Fulfilled - blank': { fulfilledType: 'blank' },
  'Fulfilled - video': { fulfilledType: 'video' },
  'Fulfilled - embed': { fulfilledType: 'embed' },
  'Fulfilled - text': { fulfilledType: 'text' },
};

const filterDefault = Object.keys(filterOptions)[0];

const createJobForm = {
  prompt: 'Run a processing job on documents of the current placeholder search:',
  params: null,
  fields: {
    task: {
      label: 'Task',
      info: 'https://github.com/voxmedia/chorus-import-manager/wiki/Document-Jobs#built-in-jobs',
      options: {}
    }
  },
  submit: 'Start Job'
};

export default {
  mixins: [Routing],
  data() {
    return {
      filterOptions,
      loaded: false,
      lookup: '',
      filter: filterDefault,
      page: Number(this.$route.query.page) || 1,
      perPage: 25,
      results: [],
      totalCount: 0,
    };
  },
  computed: {
    ...mapState([
      'currentCommunity'
    ]),
    lookupInput: {
      get() {
        return this.lookup || '';
      },
      set(val) {
        val = val || null;
        if (this.lookup === val) return;
        this.lookup = val;
        this.page = 1;
      }
    },
    filterInput: {
      get() {
        return this.filter;
      },
      set(val) {
        if (this.filter === val) return;
        this.filter = filterOptions.hasOwnProperty(val) ? val : filterDefault;
        this.page = 1;
      }
    },
    searchParams() {
      let params = {
        communityId: this.currentCommunity.id,
        pageNumber: this.page,
        perPage: this.perPage,
      };

      if (/^p\s*\d+$/.test(this.lookup)) {
        params.id = [Number(this.lookup.replace(/[^\d]*(\d+)/, '$1'))];
      } else if (/^\d+$/.test(this.lookup)) {
        params.documentEntryId = Number(this.lookup);
        params.order = 'DOCUMENT_ENTRY';
      } else if (this.lookup) {
        params.contentType = this.lookup;
        params.order = 'CONTENT_TYPE';
      }

      return Object.assign(params, filterOptions[this.filter]);
    },
    downloadUrl() {
      const params = this.searchParams;
      const url = [importManagerUrl(), `/reports/placeholders?community_id=${ params.communityId }`];
      if (params.id) url.push(`&id=${ params.id }`);
      if (params.documentEntryId) url.push(`&document_entry_id=${ params.documentEntryId }`);
      if (params.contentType) url.push(`&content_type=${ params.contentType }`);
      if (params.order) url.push(`&order=${ params.order }`);
      url.push('&columns=id,community_id,document_entry_id,content_type,data');
      return url.join('');
    }
  },
  watch: {
    page() {
      this.$router.replace({
        query: {
          community_id: this.currentCommunity.id,
          page: this.page,
        }
      });
    }
  },
  methods: {
    ...mapActions([
      'createJob',
      'fetchJobTasks',
      'setDialog',
    ]),
    formatNumber,
    jobParams() {
      const search = Object.assign({}, this.searchParams);
      delete search.pageNumber;
      delete search.perPage;
      delete search.order;
      return search;
    },
    async runJob() {
      createJobForm.params = JSON.stringify(this.jobParams());
      createJobForm.fields.task.options = await this.fetchJobTasks();

      this.setDialog({ tag: 'input-form', form: createJobForm })
        .then(async (form) => {
          const jobId = await this.createJob({
            search: {
              communityId: this.currentCommunity.id,
              placeholders: this.jobParams()
            },
            task: form.task,
          });

          this.$router.replace({
            name: 'jobs',
            query: { community_id: this.currentCommunity.id, job: jobId }
          });
        });
    }
  },
  apollo: {
    results: {
      client: 'importer',
      query: gql`
        query SearchPlaceholders($input: SearchImporterStoryPlaceholdersInput!) {
          search: searchImporterStoryPlaceholders(input: $input) {
            pageNumber
            results {
              id
              contentType
              data
              documentEntryId
              fulfilled
              fulfilledType
            }
            totalCount
          }
        }
      `,
      variables() {
        return {
          input: this.searchParams
        };
      },
      update(data) {
        this.loaded = true;
        this.page = data.search.pageNumber;
        this.totalCount = data.search.totalCount;
        return data.search.results;
      }
    }
  }
};
</script>
