<template>
  <section>
    <div v-if="$slots.default || label" class="m-import-manager__header">
      <slot><h1>{{ label }}</h1></slot>
      <res-button v-if="collapsable" paddingless class="m-l-1" @click.prevent="searchActive = !searchActive">
        <button>Search</button> <res-icon :name="searchActive ? 'minus' : 'add'" size="large" />
      </res-button>
    </div>
    <form v-if="searchActive" class="m-import-manager__fieldset" @submit.prevent>
      <res-select>
        <label>Filter</label>
        <select v-model="filterSelection">
          <option v-for="(value, label) in filterOptions" :key="label" :value="label">{{ label }}</option>
        </select>
      </res-select>
    </form>

    <sitemaps-table
      :parentId="parentId"
      :projectId="projectId"
      :status="filters.status"
      :version="version"/>
  </section>
</template>


<script>
import SitemapsTable from './sitemaps-table.vue';

const filters = {
  status: null,
};

export default {
  components: {
    SitemapsTable
  },
  props: {
    label: {
      type: String,
      default: null
    },
    collapsable: {
      type: Boolean,
      default: false
    },
    // scoping
    parentId: {
      type: Number,
      default: null
    },
    projectId: {
      type: Number,
      default: null
    },
    version: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      filterSelection: 'All',
      filterOptions: {
        'All': {},
        'New': { status: 'CREATED' },
        'Capturing': { status: 'CAPTURING' },
        'Fetched': { status: 'CAPTURED' },
        'Failed': { status: 'FAILED' },
      },
      filters: Object.assign({}, filters),
      searchActive: !this.collapsable,
    };
  },
  watch: {
    filterSelection() {
      const params = this.filterOptions[this.filterSelection];
      this.filters = Object.assign({}, filters, params);
    }
  }
};
</script>
