import VueRouter from 'vue-router';
import DashboardApp from './pages/dashboard.vue';
import IdentitiesApp from './pages/identities.vue';
import ProjectApp from './pages/project.vue';
import SitemapApp from './pages/sitemap.vue';
import DocumentApp from './pages/document.vue';
import CaptureApp from './pages/capture.vue';
import ParserApp from './pages/parser.vue';
import PlaceholdersApp from './pages/placeholders.vue';
import CommunitiesApp from './pages/communities.vue';
import JobsApp from './pages/jobs.vue';

const router = new VueRouter({
  base: '/importmanager',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: DashboardApp
    },
    {
      path: '/project/:id',
      name: 'project',
      component: ProjectApp,
      props: (route) => ({
        id: Number(route.params.id)
      })
    },
    {
      path: '/sitemap/:id',
      name: 'sitemap',
      component: SitemapApp,
      props: (route) => ({
        id: Number(route.params.id)
      })
    },
    {
      path: '/document/:id',
      name: 'document',
      component: DocumentApp,
      props: (route) => ({
        id: Number(route.params.id)
      })
    },
    {
      path: '/capture/:id',
      name: 'capture',
      component: CaptureApp,
      props: (route) => ({
        id: Number(route.params.id)
      })
    },
    {
      path: '/parser/:id',
      name: 'parser',
      component: ParserApp,
      props: (route) => ({
        id: Number(route.params.id)
      })
    },
    {
      path: '/identities/users',
      name: 'user-identities',
      component: IdentitiesApp,
      props: () => ({
        type: 'user'
      })
    },
    {
      path: '/identities/groups',
      name: 'group-identities',
      component: IdentitiesApp,
      props: () => ({
        type: 'group'
      })
    },
    {
      path: '/identities/assets',
      name: 'asset-identities',
      component: IdentitiesApp,
      props: () => ({
        type: 'asset'
      })
    },
    {
      path: '/identities/galleries',
      name: 'gallery-identities',
      component: IdentitiesApp,
      props: () => ({
        type: 'gallery'
      })
    },
    {
      path: '/placeholders',
      name: 'placeholders',
      component: PlaceholdersApp
    },
    {
      path: '/jobs',
      name: 'jobs',
      component: JobsApp
    },
    {
      path: '/communities',
      name: 'communities',
      component: CommunitiesApp
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (from.path === to.path) return next();

  const title = document.getElementsByTagName('title')[0];
  if (title) title.innerText = 'Chorus Import Manager : '+ to.name;

  const el = document.getElementById('viewport');
  if (el) el.scrollTo(0, 0);
  next();
});

export default router;
