import Vue from 'vue';
import * as cmd from './commands';
import { buildModels, buildPatches } from './models/factories';
import { defaultsByModelSet } from './models/defaults';
import sharedMutations from '../../shared/vue/store/mutations';

const modelSets = Object.keys(defaultsByModelSet);

export default {
  ...sharedMutations,

  // Sets a validated modal dialog configuration
  [cmd.SET_DIALOG](state, options) {
    state.currentDialog = options;
  },

  // Sets the table of document job tasks
  [cmd.SET_TASKS](state, tasks) {
    state.documentJobTasks = tasks.reduce((memo, task) => {
      memo[task] = task.replace(/^perform_/, '').replace('_', ' ');
      return memo;
    }, {});
  },

  // Puts one or more notifications into the messaging queue.
  [cmd.PUT_NOTICE](state, notices) {
    if (!Array.isArray(notices)) notices = [notices];
    const notifications = state.notifications.slice();

    for (let notice of notices) {
      if (notice && notice.message) {
        const message = notice.message.replace(/GraphQL error:\s*/g, '');
        const type = (notice.graphQLErrors ? 'error' : notice.type) || 'info';
        const existing = notifications.find(n => n.message === message && n.type === type);

        if (existing) {
          notice = Object.assign({}, existing, { count: existing.count + 1 });
          notifications.splice(notifications.indexOf(existing), 1, notice);
        } else {
          notifications.push({ id: Math.round(Math.random() * 100000000), count: 1, message, type });
        }
      }
    }

    state.notifications = notifications;
  },

  // Deletes a notification from the messaging queue.
  [cmd.DELETE_NOTICE](state, id) {
    state.notifications = state.notifications.filter(notice => notice.id !== id);
  },

  [cmd.SET_MODELS](state, mapping) {
    mapping = buildModels(mapping);
    for (let key of modelSets) {
      if (mapping.hasOwnProperty(key)) {
        const modelSet = Object.assign({}, state[key]);
        let models = mapping[key];
        if (!Array.isArray(models)) models = [models];

        for (let model of models) {
          modelSet[model.id] = model;
        }
        Vue.set(state, key, modelSet);
      }
    }
  },

  [cmd.PATCH_MODELS](state, mapping) {
    mapping = buildPatches(mapping);
    for (let key of modelSets) {
      if (mapping.hasOwnProperty(key)) {
        const modelSet = Object.assign({}, state[key]);
        let patches = mapping[key];
        if (!Array.isArray(patches)) patches = [patches];

        for (let patch of patches) {
          const base = modelSet[patch.id] || defaultsByModelSet[key];
          modelSet[patch.id] = Object.assign({}, base, patch);
        }
        Vue.set(state, key, modelSet);
      }
    }
  },

  [cmd.DELETE_MODELS](state, mapping) {
    for (let key of modelSets) {
      if (mapping.hasOwnProperty(key)) {
        const modelSet = Object.assign({}, state[key]);
        let models = mapping[key];
        if (!Array.isArray(models)) models = [models];

        for (let model of models) {
          delete modelSet[model.id];
        }
        Vue.set(state, key, modelSet);
      }
    }
  }
};
