import Vue from 'vue';
import Vuex from 'vuex';
import VueRouter from 'vue-router';
import VueApollo from 'vue-apollo';
import App from './app.vue';
import store from './store';
import router from './router';
import { towerClient, importerClient } from '../shared/vue/lib/apollo_client';

const configEl = document.querySelector('script[type="config"]');
const config = JSON.parse(configEl.textContent);

Vue.use(Vuex);
Vue.use(VueApollo);
Vue.use(VueRouter);

const apolloProvider = new VueApollo({
  defaultClient: towerClient,
  clients: {
    importer: importerClient,
    tower: towerClient,
  }
});

new Vue({
  router,
  apolloProvider,
  store: store(config),
  el: '#app',
  ignoredElements: res.manifest.tags.all, // eslint-disable-line no-undef
  data: () => { return { config }; }, // << kludge for community menu.
  render: h => h(App),
});
