export default {
  captureModelsList(state) {
    return Object.values(state.captureModels);
  },
  documentModelsList(state) {
    return Object.values(state.documentModels);
  },
  communityDelegationsList(state) {
    return Object.values(state.communityModels);
  },
  communityNameById(state) {
    return Object.values(state.communityModels).reduce((memo, model) => {
      memo[model.constituentCommunityId] = model.name;
      return memo;
    }, { [state.currentCommunity.id]: state.currentCommunity.name });
  },
  parserModelsList(state) {
    return Object.values(state.parserModels).sort((a, b) => String(a.name).localeCompare(b.name));
  },
  placeholderModelsList(state) {
    return Object.values(state.placeholderModels);
  },
  projectModelsList(state) {
    return Object.values(state.projectModels).sort((a, b) => String(a.name).localeCompare(b.name));
  },
  sitemapModelsList(state) {
    return Object.values(state.sitemapModels).sort((a, b) => String(a.name).localeCompare(b.name));
  }
};
