import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

export default {
  async createCommunityDelegation(store, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateCommunityDelegation($input: CreateImporterCommunityDelegationInput!) {
          delegation: createImporterCommunityDelegation(input: $input) {
            constituentCommunityId
            delegateCommunityId
            id
            name
            urlMatcher
          }
        }
      `,
      variables: { input }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(data.delegation);
  },

  async updateCommunityDelegation(store, params) {
    // params.id = Int
    // params.input = UpdateImporterCommunityDelegationInput
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateCommunityDelegation($id: Int!, $input: UpdateImporterCommunityDelegationInput!) {
          delegation: updateImporterCommunityDelegation(id: $id, input: $input) {
            constituentCommunityId
            delegateCommunityId
            id
            name
            urlMatcher
          }
        }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(data.delegation);
  },

  async lookupCommunityDelegate(store, communityId) {
    const { data } = await importerClient.query({
      query: gql`
        query LookupCommunityDelegate($id: Int!) {
          delegateId: importerLookupCommunityDelegate(constituentId: $id)
        }
      `,
      variables: { id: communityId }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(data.delegateId);
  }
};
