<template>
  <main>
    <div class="m-import-manager__header --split">
      <h1>Document jobs</h1>
    </div>
    <p class="--italic">start jobs from the documents search table</p>
    <div class="m-import-manager__table">
      <res-table class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th>Job</th>
              <th>Task</th>
              <th>Documents</th>
              <th colspan="2">Status</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="job in jobs" v-show="isVisible(job)" :key="job.id" :class="{ '--child-row': !job.workers }">
              <td>
                <template v-if="job.workers">
                  <res-button :style="{ transform: `rotate(${ activeJobId === job.id ? '0' : '-90deg' })` }" paddingless @click.prevent="toggleJob(job)">
                    <res-icon name="down" size="medium" /><button />
                  </res-button>
                  <a
                    :href="jobLogUrl(job)"
                    :title="`Logs for job ${ job.jobId }`"
                    target="_blank"
                    class="m-import-manager__digest m-r-5"
                  >
                    job {{ job.jobId.split('-')[0] }}
                  </a>
                  <span>{{ formatDateTime(job.createdAt) }}</span>
                </template>
                <template v-else>
                  <res-icon size="small" name="tab" class="m-r-1"/>
                  <a
                    :href="jobLogUrl(job)"
                    :title="`Logs for worker ${ job.id }`"
                    :data-id="job.id"
                    target="_blank"
                    class="m-import-manager__digest m-r-5"
                  >
                    worker {{ job.id }}
                  </a>
                  <span v-show="job.status === 'RUNNING' && job.lastDocumentId">@ {{ job.lastDocumentId }}</span>
                </template>
              </td>
              <td>
                <template v-if="job.workers">
                  <res-button paddingless class="m-r-5" @click.prevent="setDialog({ tag: 'text-console', value: job.searchJson })">
                    <res-icon name="publish" size="medium" /><button />
                  </res-button>
                  <span>{{ job.task.replace(/^perform_/i, '') }}</span>
                </template>
                <template v-else>
                  <span class="--italic">{{ job.startAt || '&lt;' }}..{{ job.finishAt || '&gt;' }}</span>
                </template>
              </td>
              <td>
                <span :class="{ '--italic': !job.workers }">{{ job.documentsTotal }}</span>
              </td>
              <td>
                <a :title="job.status"><res-icon :name="statusIcon(job.status)" size="medium" /></a>
                <span :class="{ '--italic': !job.workers }">{{ job.percentComplete }}%</span>
              </td>
              <td>
                <res-button v-show="mayCancel(job)" paddingless title="Cancel" @click.prevent="confirmCancelJob(job)">
                  <res-icon name="none" size="medium" /><button>Cancel</button>
                </res-button>
                <res-button v-show="mayResume(job)" paddingless title="Resume" @click.prevent="resumeJob(job.id)">
                  <res-icon name="refresh" size="medium" /><button>Resume</button>
                </res-button>
                <res-button
                  v-if="job.workers"
                  v-show="mayTrash(job)"
                  paddingless
                  title="Delete"
                  @click.prevent="confirmDeleteJob(job)"
                >
                  <res-icon name="trash" size="medium" /><button />
                </res-button>
                <res-button
                  v-if="!job.workers"
                  v-show="job.errorMessage"
                  paddingless
                  title="Errors"
                  @click.prevent="setDialog({ tag: 'text-console', value: job.errorMessage })"
                >
                  <res-icon name="warning" size="medium" /><button />
                </res-button>
              </td>
            </tr>
          </tbody>
        </table>
      </res-table>
      <footer>
        <span v-if="loaded && jobs.length">
          Found {{ formatNumber(jobRollups.length) }} jobs
        </span>
        <span v-else-if="loaded">
          No results
        </span>
      </footer>
    </div>
  </main>
</template>

<script>
import { mapActions } from 'vuex';
import { formatNumber, formatDateTime, crawlerIcons, importManagerUrl } from '../lib/formatting';
import Polling from '../components/mixins/polling';

export default {
  mixins: [Polling],
  data() {
    return {
      loaded: false,
      activeJobId: this.$route.query.job,
    };
  },
  computed: {
    jobRollups() {
      const jobsById = Object.values(this.$store.state.jobModels).reduce((memo, job) => {
        memo[job.jobId] = memo[job.jobId] || [];
        memo[job.jobId].push(job);
        return memo;
      }, {});

      return Object.keys(jobsById).map(id => {
        const workers = jobsById[id];
        const statuses = ['PENDING', 'RUNNING', 'FAILED', 'CANCELLED'];
        let status = 'COMPLETED';

        for (let s of statuses) {
          if (workers.some(j => j.status === s)) {
            status = s;
            break;
          }
        }

        return Object.assign({}, workers[0], {
          id: workers[0].jobId,
          documentsTotal: workers.reduce((t, j) => t += j.documentsTotal, 0),
          percentComplete: Math.floor(workers.reduce((t, j) => t += j.percentComplete, 0) / workers.length),
          status,
          workers,
        });
      });
    },
    jobs() {
      return this.jobRollups.reduce((memo, job) => {
        memo.push(job);
        return memo.concat(job.workers);
      }, []);
    }
  },
  watch: {
    activeJobId() {
      const query = { community_id: this.$store.state.currentCommunity.id };
      if (this.activeJobId) query.job = this.activeJobId;
      this.$router.replace({ query });
    },
    '$route.query'() {
      const query = this.$route.query;
      if (query.job) this.activeJobId = query.job;
    }
  },
  mounted() {
    this.startImmediatePolling(() => {
      this.fetchJobs().then(() => this.loaded = true);
    }, 10000);
  },
  methods: {
    ...mapActions([
      'cancelJob',
      'deleteJob',
      'fetchJobs',
      'resumeJob',
      'setDialog',
    ]),
    formatDateTime,
    formatNumber,
    statusIcon(status) {
      return crawlerIcons[status];
    },
    toggleJob(job) {
      this.activeJobId = (job.jobId === this.activeJobId) ? null : job.jobId;
    },
    isVisible(job) {
      return job.workers || job.jobId === this.activeJobId;
    },
    mayCancel(job) {
      return job.status === 'RUNNING' || (job.workers && job.workers.some(j => j.status === 'RUNNING'));
    },
    mayResume(job) {
      return !job.workers && ['CANCELLED', 'FAILED'].includes(job.status);
    },
    mayTrash(job) {
      return ['COMPLETED', 'CANCELLED', 'FAILED'].includes(job.status);
    },
    confirm(job, action) {
      const workers = job.workers ? ' and all of its workers' : '';
      return confirm(`${ action } this job${ workers }?`);
    },
    confirmCancelJob(job) {
      if (this.confirm(job, 'Cancel')) {
        this.cancelJob(job.id);
      }
    },
    confirmDeleteJob(job) {
      if (this.confirm(job, 'Delete')) {
        this.deleteJob(job.id);
      }
    },
    jobLogUrl(job) {
      const path = [importManagerUrl()];
      path.push(job.workers ? `reports/job/${ job.jobId }` : `reports/job/worker/${ job.id }`);
      return path.join('/');
    }
  }
};
</script>
