<template>
  <tr>
    <td>
      <res-icon v-if="isSitemap" size="small" name="tab" class="m-r-1"/>
      <svg
        v-if="isProject && !model.active"
        class="m-r-5"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        viewBox="0 0 401.998 401.998"
        width="1em"
        height="1em"
      >
        <path d="M357.45,190.721c-5.331-5.33-11.8-7.993-19.417-7.993h-9.131v-54.821c0-35.022-12.559-65.093-37.685-90.218 C266.093,12.563,236.025,0,200.998,0c-35.026,0-65.1,12.563-90.222,37.688C85.65,62.814,73.091,92.884,73.091,127.907v54.821 h-9.135c-7.611,0-14.084,2.663-19.414,7.993c-5.33,5.326-7.994,11.799-7.994,19.417V374.59c0,7.611,2.665,14.086,7.994,19.417 c5.33,5.325,11.803,7.991,19.414,7.991H338.04c7.617,0,14.085-2.663,19.417-7.991c5.325-5.331,7.994-11.806,7.994-19.417V210.135 C365.455,202.523,362.782,196.051,357.45,190.721z M274.087,182.728H127.909v-54.821c0-20.175,7.139-37.402,21.414-51.675 c14.277-14.275,31.501-21.411,51.678-21.411c20.179,0,37.399,7.135,51.677,21.411c14.271,14.272,21.409,31.5,21.409,51.675V182.728 z"/>
      </svg>
      <router-link :to="routerLink">{{ model.name }}</router-link>
    </td>
    <td>
      <abbr v-if="isSitemap && model.hasRunningCrawler" title="RUNNING">
        <res-icon name="loading" size="medium"/>
      </abbr>
      <abbr v-else-if="isSitemap" title="INACTIVE">
        <res-icon name="minus-circle" size="medium"/>
      </abbr>
    </td>
    <td>{{ aggregate('activeDocumentsCount') }}</td>
    <td>{{ aggregate('importedDocumentsCount') }}</td>
    <td>{{ aggregate('failedDocumentsCount') }}</td>
    <td>{{ aggregate('newDocumentsCount') }}</td>
    <td class="--split">
      {{ aggregate('archivedDocumentsCount') }}
      <res-button v-if="isSitemap" :disabled="recounting" paddingless @click.prevent="recount(model.id)">
        <res-icon name="refresh" size="medium" /><button />
      </res-button>
    </td>
  </tr>
</template>

<script>
import { formatNumber } from '../../lib/formatting';

export default {
  props: {
    model: { type: Object, required: true }
  },
  data() {
    return {
      recounting: false
    };
  },
  computed: {
    isProject() {
      return this.model._type === 'project';
    },
    isSitemap() {
      return this.model._type === 'sitemap';
    },
    childSitemaps() {
      return this.isProject ? this.$store.getters.sitemapModelsList.filter(sitemap => sitemap.projectId === this.model.id) : [];
    },
    routerLink() {
      return {
        name: this.model._type,
        params: { id: this.model.id },
        query: { community_id: this.$store.state.currentCommunity.id }
      };
    }
  },
  methods: {
    aggregate(field) {
      if (!this.isProject) return formatNumber(this.model[field]);

      return formatNumber(this.childSitemaps.reduce((memo, sitemap) => {
        if (memo === null || sitemap[field] === null) return null;
        return memo += sitemap[field];
      }, 0));
    },
    recount(sitemapId) {
      this.recounting = true;
      this.$store.dispatch('recountSitemap', { id: sitemapId, deep: true })
        .then(() => this.recounting = false)
        .catch(() => this.recounting = false);
    }
  }
};
</script>
