<template>
  <tr>
    <td style="width:25%;">
      <span v-if="!editing">{{ mutable.name }}</span>
      <res-input v-else>
        <input v-model.lazy="mutable.name" type="text" />
      </res-input>
    </td>
    <td style="width:20%;">
      <span v-if="!editing">{{ mutable.constituentCommunityId }}</span>
      <res-input v-else>
        <input v-model.lazy.number="mutable.constituentCommunityId" type="text" />
      </res-input>
    </td>
    <td style="width:40%;">
      <span v-if="!editing">{{ mutable.urlMatcher }}</span>
      <res-input v-else>
        <input v-model.lazy.number="mutable.urlMatcher" type="text" />
      </res-input>
    </td>
    <td style="width:5%;">
      <res-button :style="editStyle" paddingless @click.prevent="toggle">
        <res-icon name="edit" size="medium" /><button />
      </res-button>
    </td>
  </tr>
</template>

<script>
import { formatMutable, mutableHasChanged } from '../../lib/utils';

const mutableSchema = {
  constituentCommunityId: '',
  name: '',
  urlMatcher: '',
};

export default {
  props: {
    model: { type: Object, required: true },
  },
  data() {
    return {
      editing: false,
      mutable: formatMutable(mutableSchema, this.model),
    };
  },
  computed: {
    isChanged() {
      return mutableHasChanged(this.mutable, this.model);
    },
    editStyle() {
      return this.editing ? 'transform:rotate(45deg)' : null;
    }
  },
  methods: {
    save() {
      if (!this.isChanged) return;
      this.$store.dispatch('updateCommunityDelegation', { id: this.model.id, input: this.mutable })
        .then(result => {
          this.mutable = formatMutable(mutableSchema, result);
        });
    },
    toggle() {
      if (this.editing) this.save();
      this.editing = !this.editing;
    }
  }
};
</script>
