<template>
  <form @submit.prevent>
    <fieldset>
      <res-input>
        <label>Project Name</label>
        <input v-model="name" type="text" />
      </res-input>
    </fieldset>
    <fieldset>
      <res-select v-if="parserModelsList.length">
        <label>Parser</label>
        <select v-model="documentParserId">
          <option v-for="parser in parserModelsList" :key="parser.id" :value="parser.id">{{ parser.name }}</option>
        </select>
      </res-select>
      <res-input v-else>
        <label>Parser Name</label>
        <input v-model="documentParserName" type="text" />
      </res-input>
    </fieldset>
    <footer>
      <res-button design="primary" @click.prevent="create()">
        <button type="submit">Create Project</button>
      </res-button>
      <res-button design="secondary" class="m-l-1" @click.prevent="close()">
        <button type="button">Cancel</button>
      </res-button>
    </footer>
  </form>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  data() {
    const parsers = this.$store.getters.parserModelsList;
    return {
      name: '',
      documentParserId: parsers.length ? parsers[0].id : '',
      documentParserName: '',
    };
  },
  computed: {
    ...mapState([
      'currentCommunity'
    ]),
    ...mapGetters([
      'parserModelsList'
    ])
  },
  methods: {
    ...mapActions([
      'createParser',
      'createProject',
      'setDialog',
    ]),
    create() {
      this.prebuildParser()
        .then(parser => this.createProject({ name: this.name, documentParserId: parser.id }))
        .then(this.close);
    },
    prebuildParser() {
      const parser = this.$store.state.parserModels[this.documentParserId];
      if (parser) return Promise.resolve(parser);
      return this.createParser({ name: this.documentParserName });
    },
    close() {
      this.setDialog(null);
    }
  }
};
</script>
