import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

export const CaptureBaseAttrs = gql`
  fragment CaptureBaseAttrs on ImporterDocumentCapture {
    contentApiOpsDigest
    createdAt
    documentEntryId
    error
    errorDigest
    fetchDigest
    fetchStatus
    id
    importStatus
    parseDigest
    parseStatus
    redirectsStatus
  }
`;

const DocumentDisplayAttrs = gql`
  fragment DocumentDisplayAttrs on ImporterDocumentEntry {
    archived
    archivalStatus
    canonicalUrl
    chorusEntryId
    chorusEntryUrl
    contentApiUuid
    fetchedAt
    id
    importedDocumentCaptureId
    status
  }
`;

export default {

  async fetchCapture({ state }, params) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchDocumentCapture($id: Int!) {
          capture: importerDocumentCapture(id: $id) {
            ...CaptureBaseAttrs
            body
            documentEntry {
              ...DocumentDisplayAttrs
              allowsCapture
              project {
                id
                name
                documentParser {
                  id
                  name
                }
              }
              sitemapEntry {
                id
                name
                parentSitemap {
                  id
                  name
                }
              }
            }
            errorMessage
            errorTrace
            httpStatus
            redirectUrl
            storyJson
            contentApiOps
          }
        }
        ${ CaptureBaseAttrs }
        ${ DocumentDisplayAttrs }
      `,
      variables: { id: params.id }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.SET_MODELS, {
      data,
      documentModels: 'capture.documentEntry',
      captureModels: 'capture',
      sitemapModels: ['capture.documentEntry.sitemapEntry', 'capture.documentEntry.sitemapEntry.parentSitemap'],
      parserModels: 'capture.documentEntry.project.documentParser',
      projectModels: 'capture.documentEntry.project',
    });
    return Promise.resolve(state.captureModels[data.capture.id]);
  },

  async processCapture({ state }, params) {
    // params.id = Int
    // params.action = ImporterDocumentCaptureAction
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation ProcessDocumentCapture($id: Int!, $action: ImporterDocumentCaptureAction!) {
          capture: processImporterDocumentCapture(id: $id, action: $action) {
            ...CaptureBaseAttrs
            documentEntry {
              ...DocumentDisplayAttrs
            }
            errorMessage
            errorTrace
            httpStatus
            storyJson
            contentApiOps
          }
        }
        ${ CaptureBaseAttrs }
        ${ DocumentDisplayAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, {
      captureModels: data.capture,
      documentModels: data.capture.documentEntry,
    });
    return Promise.resolve(state.captureModels[params.id]);
  },

  unloadCapture({ state }, params) {
    const model = state.captureModels[params.id];
    this.commit(cmd.DELETE_MODELS, {
      captureModels: model,
      documentModels: state.documentModels[model.documentEntryId],
    });
  }
};
