<template>
  <section>
    <div v-if="$slots.default || label || collapsable" class="m-import-manager__header">
      <slot><h1>{{ label }}</h1></slot>
      <res-button v-if="collapsable" paddingless class="m-l-1" @click.prevent="searchActive = !searchActive">
        <button>Search</button> <res-icon :name="searchActive ? 'minus' : 'add'" size="large" />
      </res-button>
      <res-button v-if="sitemapEntryId" paddingless class="m-l-a" @click.prevent="addDocument()">
        <res-icon name="versions" size="large" /> <button>Add document</button>
      </res-button>
    </div>
    <form v-if="searchActive" class="m-import-manager__fieldset" @submit.prevent>
      <res-input style="min-width:40%;">
        <label>Lookup</label>
        <input v-model.lazy="lookupInput" type="text" placeholder="URL or Error Code" />
        <res-icon slot="inner-right" name="search" />
      </res-input>
      <res-select v-if="multiCommunity && !communityScopeId" class="m-l-1">
        <label>Community</label>
        <select v-model="communityScopeInput">
          <option value="ALL">All</option>
          <option :value="communityId">{{ $store.state.currentCommunity.name }}</option>
          <option v-for="d in communityDelegationsList" :key="d.constituentCommunityId" :value="d.constituentCommunityId">{{ d.name }}</option>
        </select>
      </res-select>
      <res-select class="m-l-1">
        <label>Filter</label>
        <select v-model="filterInput">
          <option v-for="(value, label) in filterOptions" :key="label" :value="label">{{ label }}</option>
        </select>
      </res-select>
      <res-select v-if="scopingEnabled" class="m-l-1">
        <label>Project</label>
        <select v-model="projectInput">
          <option value="">All</option>
          <option v-for="project in projects" :key="project.id" :value="project.id">{{ project.name }}</option>
        </select>
      </res-select>
      <res-select v-if="scopingEnabled" :disabled="!projectInput" class="m-l-1">
        <label>Sitemap</label>
        <select v-model="sitemapInput">
          <option value="">All</option>
          <option v-for="sitemap in sitemapsForProject" :key="sitemap.id" :value="sitemap.id">{{ sitemap.name }}</option>
        </select>
      </res-select>
    </form>

    <div class="m-import-manager__table">
      <res-table zebra="true" class="m-dashboard__table">
        <table>
          <thead>
            <tr>
              <th class="--icon-only" />
              <th class="--url">URL</th>
              <th v-if="multiCommunity" class="--community">Community</th>
              <th class="--status">Status</th>
              <th class="--modified">Last Update</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="doc in results" :key="doc.id">
              <td class="--icon-only">
                <router-link :to="routeTo('document', doc.id)">
                  <res-icon name="versions" size="medium" />
                </router-link>
              </td>
              <td class="--url">
                <a :href="doc.canonicalUrl">{{ stripURIHost(doc.canonicalUrl) }}</a>
                <a :href="doc.chorusEntryUrl">{{ stripURIHost(doc.chorusEntryUrl) }}</a>
              </td>
              <th v-if="multiCommunity" class="--community">
                {{ communityNameById[doc.communityId] }}
              </th>
              <td :class="{ 'is-watched': doc.watched }" class="--status">
                <a :title="doc.status">
                  <res-icon :name="statusIcon(doc)" size="medium" />
                </a>
                <a v-if="doc.queued" title="QUEUED">
                  <res-icon name="clock" size="medium" />
                </a>
                <a v-if="doc.archived" :title="doc.archivalStatus">
                  <res-icon name="none" size="medium" />
                </a>
                <a v-if="doc.watched" title="STARRED">
                  <res-icon name="star" size="medium" />
                </a>
                <router-link
                  v-if="doc.errorDigest"
                  :to="routeTo('capture', doc.failedDocumentCaptureId)"
                  :title="doc.errorSummary"
                  class="m-import-manager__digest --error"
                >
                  {{ doc.errorDigest.slice(0, 8) }}
                  <template v-if="doc.errorOccurrenceCount">({{ doc.errorOccurrenceCount }})</template>
                </router-link>
              </td>
              <td class="--modified">{{ formatDateTime(doc.updatedAt) }}</td>
            </tr>
          </tbody>
        </table>
      </res-table>
      <footer>
        <span v-if="loaded">
          <a href="#" title="Refresh" @click.prevent="refresh()">
            <res-icon :animated="String(fetching)" name="loading" size="small"/>
          </a>
          Found {{ formatNumber(totalCount) }} results
          <span v-show="totalCount > 0">
            [<a :href="downloadUrl">download</a> / <a :href="downloadUrl" @click.prevent="runJob">job</a>]
          </span>
        </span>
        <res-pagination
          :page="page"
          :count="totalCount"
          :display="perPage"
          indicator="page"
          pagination="text-only"
          navigation="text"
          @change="page=$event.target.page"
        />
      </footer>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { debounce } from '../lib/utils';
import { formatDateTime, formatNumber, stripURIHost, entryStatusIcons, formatFinder, importManagerUrl } from '../lib/formatting';
import Routing from './mixins/routing';

const filterOptions = {
  'All': {},
  'Imported': { status: 'CAPTURED' },
  'Failed': { status: 'FAILED' },
  'Failed – unique': { status: 'FAILED', view: 'UNIQUE' },
  'Archived': { archived: true },
  'Archived – admin': { archivalStatus: 'ARCHIVED_BY_ADMIN' },
  'Archived – crawler': { archivalStatus: 'ARCHIVED_BY_CRAWLER' },
  'Archived – editorial': { archivalStatus: 'ARCHIVED_BY_EDITORIAL' },
  'Archived – forcefix': { archivalStatus: 'ARCHIVED_BY_MANUAL_UPDATE' },
  'New': { status: 'CREATED' },
  'Processing': { status: 'CAPTURING' },
  'Starred': { watched: true },
};

const filterDefault = Object.keys(filterOptions)[0];

const createDocumentForm = {
  prompt: 'Add a document URL for testing. It will shift to its appropraite sitemap once crawled.',
  fields: {
    canonicalUrl: {
      label: 'Canonical URL',
      type: 'text'
    }
  },
  submit: 'Add Document'
};

const createJobForm = {
  prompt: 'Run a processing job on the current document search:',
  params: null,
  fields: {
    task: {
      label: 'Task',
      info: 'https://github.com/voxmedia/chorus-import-manager/wiki/Document-Jobs#built-in-jobs',
      options: {}
    },
    id: {
      label: 'Document IDs (optional)',
      type: 'textarea',
      placeholder: 'Separated by commas or newlines'
    }
  },
  submit: 'Start Job'
};

function slugify(str) {
  return str.replace(/[(),– ]/g, '').toLowerCase();
}

export default {
  mixins: [Routing],
  props: {
    label: {
      type: String,
      default: null
    },
    collapsable: {
      type: Boolean,
      default: false
    },
    sitemapEntryId: {
      type: Number,
      default: null
    },
    communityScopeId: {
      type: Number,
      default: null
    },
    projects: {
      type: Object,
      // default: () => ({})
    },
    version: {
      type: Number,
      default: null
    },
  },
  data() {
    return {
      filterOptions,
      searchActive: !this.collapsable,
      downloadUrl: null,

      // search parameters
      communityScope: this.communityScopeId ? String(this.communityScopeId) : 'ALL',
      projectId: null,
      sitemapId: this.sitemapEntryId || null, // internal value defaults to external prop
      filter: filterDefault,
      lookup: null,
      page: 1,

      // request data
      perPage: 25,
      totalCount: 0,
      results: [],
      fetching: false,
      loaded: false,
    };
  },
  computed: {
    ...mapGetters([
      'communityDelegationsList',
      'communityNameById'
    ]),
    communityId() {
      return this.$store.state.currentCommunity.id;
    },
    multiCommunity() {
      return !!this.communityDelegationsList.length;
    },
    scopingEnabled() {
      // selecting project/sitemap scopes requires
      // projects metadata and no hard-coded scoping IDs.
      return this.projects && !this.sitemapEntryId;
    },
    sitemapsForProject() {
      const project = (this.projects || {})[this.projectId];
      return project ? project.sitemaps : [];
    },
    searchParams() {
      let params = {
        communityId: this.communityId,
        pageNumber: this.page,
        perPage: this.perPage,
      };

      if (this.multiCommunity) {
        if (this.communityScope === 'ALL') {
          params.selectCommunities = this.communityDelegationsList.map(d => d.constituentCommunityId).concat([this.communityId]);
        } else if (this.communityScope && Number(this.communityScope) !== this.communityId) {
          params.selectCommunities = [Number(this.communityScope)];
        }
      }

      if (this.projectId !== null) params.projectId = this.projectId;
      if (this.sitemapId !== null) params.sitemapEntryId = this.sitemapId;
      if (this.lookup !== null) {
        params = Object.assign(params, formatFinder(this.lookup));
      }
      return Object.assign(params, filterOptions[this.filter]);
    },
    lookupInput: {
      get() {
        return this.lookup || '';
      },
      set(val) {
        val = val || null;
        if (this.lookup === val) return;
        this.lookup = val;
        this.page = 1;
      }
    },
    communityScopeInput: {
      get() {
        return this.communityScope;
      },
      set(val) {
        if (this.communityScope === val) return;
        this.communityScope = val;
        this.page = 1;
      }
    },
    filterInput: {
      get() {
        return this.filter;
      },
      set(val) {
        if (this.filter === val) return;
        this.filter = filterOptions.hasOwnProperty(val) ? val : filterDefault;
        this.page = 1;
      }
    },
    projectInput: {
      get() {
        return this.projectId || '';
      },
      set(val) {
        val = Number(val) || null;
        if (this.projectId === val) return;
        this.projectId = val;
        this.sitemapId = null;
        this.page = 1;
      }
    },
    sitemapInput: {
      get() {
        return this.sitemapId || '';
      },
      set(val) {
        val = Number(val) || null;
        if (this.sitemapId === val) return;
        this.sitemapId = val;
        this.page = 1;
      }
    }
  },
  watch: {
    communityScopeId(val) {
      this.communityScope = val ? String(val) : 'ALL';
    },
    searchParams() {
      this.refresh();
    },
    version() {
      this.refresh();
    },
    '$route.query'() {
      this.pullRoute();
    }
  },
  mounted() {
    this.pullRoute();
  },
  methods: {
    ...mapActions([
      'findOrCreateDocument',
      'searchDocuments',
      'fetchJobTasks',
      'createJob',
      'setDialog',
    ]),
    formatDateTime,
    formatNumber,
    stripURIHost,
    statusIcon(doc) {
      return entryStatusIcons[doc.status] || entryStatusIcons.CREATED;
    },
    addDocument() {
      if (!this.sitemapEntryId) throw('a sitemap prop is required');
      this.setDialog({ tag: 'input-form', form: createDocumentForm })
        .then(async (data) => {
          if (!data.canonicalUrl) return;
          const doc = await this.findOrCreateDocument({
            canonicalUrl: data.canonicalUrl,
            communityId: this.communityId,
            sitemapEntryId: this.sitemapEntryId,
            watched: true,
          });

          this.$router.replace(this.routeTo('document', doc.id));
        });
    },
    refresh: debounce(function() {
      this.pushRoute();
      this.search();
    }),
    pullRoute() {
      const query = this.$route.query;
      if (query.q) this.lookup = query.q || null;
      if (query.scope) this.communityScope = query.scope || 'ALL';
      if (query.project_id) this.projectId = Number(query.project_id) || null;
      if (query.sitemap_id) this.sitemapId = Number(query.sitemap_id) || null;
      if (query.filter) this.filter = Object.keys(filterOptions).find(key => slugify(key) === query.filter) || filterDefault;
      if (query.page) this.page = Number(query.page) || 1;
    },
    pushRoute() {
      const query = { community_id: this.communityId };
      if (this.communityScope !== 'ALL') query.scope = this.communityScope;
      if (this.projectId !== null) query.project_id = this.projectId;
      if (this.sitemapId !== null && !this.sitemapEntryId) query.sitemap_id = this.sitemapId;
      if (this.lookup !== null) query.q = this.lookup;
      if (this.filter !== filterDefault) query.filter = slugify(this.filter);
      if (this.page > 1) query.page = this.page;
      this.$router.replace({ query });
    },
    search() {
      this.setDownloadUrl();
      this.fetching = true;
      this.searchDocuments(this.searchParams).then(search => {
        this.loaded = true;
        this.fetching = false;
        this.totalCount = search.totalCount;
        this.results = search.results;
      });
    },
    setDownloadUrl() {
      const params = this.searchParams;
      const url = [importManagerUrl(), `/reports/documents?community_id=${ this.communityId }`];
      if (params.selectCommunities) url.push(`&select_communities=${ params.selectCommunities.join(',') }`);
      if (params.contentApiUuid) url.push(`&content_api_uuid=${ params.contentApiUuid }`);
      if (params.findByUrl) url.push(`&find_by_url=${ params.findByUrl }`);
      if (params.errorDigest) url.push(`&error_digest=${ params.errorDigest }`);
      if (params.chorusEntryId) url.push(`&chorus_entry_id=${ params.chorusEntryId }`);
      if (params.projectId) url.push(`&project_id=${ params.projectId }`);
      if (params.sitemapEntryId) url.push(`&sitemap_entry_id=${ params.sitemapEntryId }`);
      if (params.archivalStatus) url.push(`&archival_status=${ params.archivalStatus.toLowerCase() }`);
      if (params.archived) url.push('&archived=true');
      if (params.watched) url.push('&watched=true');
      if (params.view) url.push(`&view=${ params.view.toLowerCase() }`);
      if (params.status) url.push(`&status=${ params.status.toLowerCase() }`);
      if (params.status === 'FAILED') {
        url.push('&columns=id,canonical_url,failed_capture_url,error_type,error_message');
      } else {
        url.push('&columns=id,canonical_url,chorus_entry_url,chorus_entry_id,document_entry_url,status,watched,modified_at');
      }

      this.downloadUrl = url.join('');
    },
    jobParams(ids='') {
      ids = ids.split(/\D/).filter(id => /\d+/.test(id)).map(Number);

      const search = Object.assign({}, this.searchParams);
      delete search.pageNumber;
      delete search.perPage;
      if (ids.length) search.id = ids;
      return search;
    },
    async runJob() {
      createJobForm.params = JSON.stringify(this.jobParams());
      createJobForm.fields.task.options = await this.fetchJobTasks();

      this.setDialog({ tag: 'input-form', form: createJobForm })
        .then(async (form) => {
          const jobId = await this.createJob({
            search: this.jobParams(form.id),
            task: form.task,
          });

          this.$router.replace({
            name: 'jobs',
            query: {
              community_id: this.communityId,
              job: jobId,
            }
          });
        });
    }
  }
};
</script>
