import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';
import { formatFinder } from '../../lib/formatting';

export default {
  async searchDocuments({ state }, params) {
    let input = Object.assign({}, params);
    input.communityId = state.currentCommunity.id;
    input.pageNumber = input.pageNumber || 1;
    input.perPage = input.perPage || 5;

    if (input.findBy) {
      input = Object.assign(input, formatFinder(input.findBy));
      delete input.findBy;
    }

    const { data } = await importerClient.query({
      query: gql`
        query SearchDocuments($input: SearchImporterDocumentEntriesInput!, $errorScope: Int) {
          search: searchImporterDocumentEntries(input: $input) {
            pageNumber
            results {
              archived
              archivalStatus
              canonicalUrl
              chorusEntryUrl
              communityId
              errorDigest
              errorOccurrenceCount(communityId: $errorScope)
              errorSummary
              failedDocumentCaptureId
              fetchedAt
              id
              queued
              status
              updatedAt
              watched
            }
            totalCount
          }
        }
      `,
      variables: {
        errorScope: input.view === 'UNIQUE' ? input.communityId : null,
        input
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    return Promise.resolve(data.search);
  }
};
