import gql from 'graphql-tag';
import * as cmd from '../commands';
import { importerClient } from '../../../shared/vue/lib/apollo_client';

const ProjectBaseAttrs = gql`
  fragment ProjectBaseAttrs on ImporterProject {
    active
    createRedirects
    createAuthors
    documentParserId
    enableFrontPage
    id
    name
  }
`;

export default {

  // fetch all projects
  async fetchProjects({ state }) {
    const { data } = await importerClient.query({
      query: gql`
        query FetchProjectsAndParsers($communityId: Int!) {
          communities: importerCommunityDelegations(delegateId: $communityId) {
            constituentCommunityId
            id
            name
          }
          parsers: importerDocumentParsers(communityId: $communityId) {
            id
            name
          }
          projects: importerProjects(communityId: $communityId) {
            ...ProjectBaseAttrs
            sitemapEntries(root: true) {
              activeDocumentsCount
              archivedDocumentsCount
              failedDocumentsCount
              hasRunningCrawler
              id
              importedDocumentsCount
              name
              newDocumentsCount
              projectId
            }
          }
        }
        ${ ProjectBaseAttrs }
      `,
      variables: {
        communityId: state.currentCommunity.id,
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    const sitemapsData = data.projects.reduce((memo, p) => memo.concat(p.sitemapEntries), []);

    this.commit(cmd.PATCH_MODELS, {
      parserModels: data.parsers,
      projectModels: data.projects,
      sitemapModels: sitemapsData,
      communityModels: data.communities,
    });
    return Promise.resolve();
  },

  // fetch all projects, but return a single one
  async fetchProject({ state }, params) {
    return await this.dispatch('fetchProjects')
      .then(() => Promise.resolve(state.projectModels[params.id]));
  },

  async createProject({ state }, input) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation CreateProject($input: CreateImporterProjectInput!) {
          project: createImporterProject(input: $input) {
            ...ProjectBaseAttrs
          }
        }
        ${ ProjectBaseAttrs }
      `,
      variables: {
        input: Object.assign({
          communityId: state.currentCommunity.id,
          networkId: state.network.id,
        }, input)
      }
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { projectModels: data.project });
    return Promise.resolve(state.projectModels[data.project.id]);
  },

  async updateProject({ state }, params) {
    const { data } = await importerClient.mutate({
      mutation: gql`
        mutation UpdateProject($id: Int!, $input: UpdateImporterProjectInput!) {
          project: updateImporterProject(id: $id, input: $input) {
            ...ProjectBaseAttrs
          }
        }
        ${ ProjectBaseAttrs }
      `,
      variables: params
    }).catch(err => this.commit(cmd.PUT_NOTICE, err));

    this.commit(cmd.PATCH_MODELS, { projectModels: data.project });
    return Promise.resolve(state.projectModels[params.id]);
  }
};
