import { SET_DIALOG } from '../commands';

export default {

  setDialog(store, params) {
    if (!params) return this.commit(SET_DIALOG, null);
    if (typeof params === 'string') params = { tag: params };

    const promise = new Promise((resolve, reject) => {
      params.resolve = resolve;
      params.reject = reject;
    });

    this.commit(SET_DIALOG, params);
    return promise;
  }

};
