// Parses a string to a date object, or null.
export function parseDate(str) {
  const date = new Date(str || undefined);
  return date.getTime() ? date : null;
}

// Formats a time string as `1/1/2019`
export function formatDate(str) {
  const date = parseDate(str);
  return date ? `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}` : '';
}

// Formats a time string as `12:07pm`
export function formatTime(str) {
  const date = parseDate(str);
  if (date) {
    const hours = ('00' + date.getHours()).substr(-2, 2);
    const mins = ('00' + date.getMinutes()).substr(-2, 2);
    return `${hours}:${mins}`;
  }
  return '';
}

// Formats a time string as `1/1/2019 12:07pm`
export function formatDateTime(str) {
  const date = formatDate(str);
  const time = formatTime(str);
  return `${date} ${time}`;
}

// Formats a seconds duration as HH:MM:SS.
export function formatHMS(secs) {
  secs = Math.round(secs);
  const hours   = Math.floor(secs / 3600);
  const minutes = Math.floor((secs - (hours * 3600)) / 60);
  const seconds = secs - (hours * 3600) - (minutes * 60);

  return [hours, minutes, seconds]
    .map((t) => '00' + t)
    .map((t) => t.substr(t.length-2))
    .join(':');
}

export function stripURIHost(str) {
  return String(str || '').trim().replace(/^(https?:)?\/\/[^/]+\//, '/');
}

export function formatNumber(num) {
  return typeof num === 'number' ? num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : num;
}

export function formatFinder(finder) {
  finder = finder.trim();
  let field = 'findByUrl';
  let value = finder;

  const sbnEntryId = /sbn\s*(\d+)/i;
  const contentApiUuid = /^uuid\s*([a-f0-9-]+)$/i;
  const errorDigest = /^[0-9a-f]{8,32}$/i;

  if (sbnEntryId.test(finder)) {
    field = 'chorusEntryId';
    value = Number(finder.match(sbnEntryId)[1]);
  } else if (contentApiUuid.test(finder)) {
    field = 'contentApiUuid';
    value = finder.match(contentApiUuid)[1];
  } else if (errorDigest.test(finder)) {
    field = 'errorDigest';
  }

  return { [field]: value };
}

// @todo – hook this into application config
export function importManagerUrl() {
  const sandbox = /\.(sb\d+\.k8s\.(?:sbndev|voxops)\.net)/;
  if (/localhost/.test(location.host)) {
    return 'http://chorus-import-manager.local.sbndev.net:4444';
  } else if (sandbox.test(location.host)) {
    return 'https://chorus-import-manager.'+location.host.match(sandbox)[1];
  } else {
    return 'https://chorus-import-manager.voxops.net';
  }
}
